.section___reviews {
  padding: 60px 0;
  position: relative;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.reviews__container {
  position: relative;
  z-index: 5;
}

.reviews__title {
  font-size: 42px;
  line-height: 1.23;
  font-weight: 500;
  text-align: center;
  color: #fff;
}

.reviews__content {
  margin-top: 48px;
}

.reviews__carousel {
  padding: 0 64px;
  padding-bottom: 48px;
  position: relative;

  .owl-nav {
    div {
      position: absolute;
      top: 100px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: $turquoise;
      font-size: 0;
      transition: .3s;
      background-repeat: no-repeat;
      background-position: center center;
      -webkit-background-size: 8px;
      background-size: 8px;
      background-image: url("data:image/svg+xml,%3Csvg role='presentation' focusable='false' style='display: block' viewBox='0 0 9.3 17' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cstyle type='text/css'%3E %23rec211828607 .t-slds__arrow_wrapper polyline %7B%0A-webkit-transition: stroke ease-in-out .2s;%0A-moz-transition: stroke ease-in-out .2s;%0A-o-transition: stroke ease-in-out .2s;%0Atransition: stroke ease-in-out .2s;%0A%7D%0A%23rec211828607 .t-slds__arrow_wrapper:hover polyline %7B%0Astroke: %23001c45 !important; %7D%0A%3C/style%3E%3Cdesc%3ELeft%3C/desc%3E%3Cpolyline fill='none' stroke='%23ffffff' stroke-linejoin='butt' stroke-linecap='butt' stroke-width='1' points='0.5,0.5 8.5,8.5 0.5,16.5'%3E%3C/polyline%3E%3C/svg%3E");

      &.disabled {
        opacity: 0;
        visibility: hidden;
      }

      &.owl-prev {
        left: 0;
        transform: rotate(180deg);
      }

      &.owl-next {
        right: 0;
      }
    }
  }

  .owl-dots {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;

    div {
      width: 8px;
      height: 8px;
      margin: 0 5px;
      border-radius: 50%;
      background-color: $turquoise;
      transition: .3s;
      
      &:hover, &.active {
        background-color: #fff;
      }
    }
  }
}

.reviews__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  text-align: center;
  color: #fff;
}

.reviews__ava {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
}

.reviews__maintext {
  margin-top: 40px;
  font-size: 22px;
  font-weight: 300;
}

.reviews__name {
  margin-top: 40px;
  font-size: 22px;
  line-height: 1.36;
  font-weight: 600;
}

.reviews__small {
  margin-top: 4px;
  font-weight: 300;
}

.reviews__soc {
  margin-top: 16px;

  li {
    margin-right: 4px;

    &:last-child {
      margin: 0;
    }

    a {
      display: block;

      img {
        width: 23px;
        height: auto;
        display: block;
      }
    }
  }
}




@media screen and (max-width: 1240px) {
  .reviews__maintext {
    font-size: 20px;
  }
  .reviews__name {
    margin-top: 32px;
    font-size: 20px;
  }
  .reviews__small {
    font-size: 12px;
  }
  .reviews__soc {
    margin-top: 8px;
  }
}


@media screen and (max-width: 980px) {
  .section___reviews {
    padding: 40px 0;
  }
  .reviews__carousel {
    padding-bottom: 30px;
  }
  .reviews__title {
    font-size: 30px;
  }
  .reviews__content {
    margin-top: 30px;
  }
}


@media screen and (max-width: 767px) {
  .section___reviews {
    padding: 40px 0;
  }
  .reviews__carousel {
    padding: 0 40px;
    padding-bottom: 30px;
    .owl-nav {
      div {
        top: 200px;
        width: 24px;
        height: 24px;
        -webkit-background-size: 6px;
        background-size: 6px;
      }
    }
  }
  .reviews__maintext {
    font-size: 18px;
    margin-top: 30px;
  }
  .reviews__name {
    margin-top: 24px;
    font-size: 18px;
  }
}


@media screen and (max-width: 480px) {
  .reviews__title {
    font-size: 26px;
  }
  .reviews__carousel {
    padding: 0;
    padding-bottom: 30px;
    .owl-nav {
      div {
        top: 38px;
      }
    }
  }
  .reviews__ava {
    width: 100px;
    height: 100px;
  }
  .reviews__maintext {
    margin-top: 20px;
    font-size: 14px;
    line-height: 1.3;
  }
  .reviews__name {
    margin-top: 20px;
  }
}