.section__success {
  padding: 60px 0;
  background-color: #001029;
  position: relative;
  overflow: hidden;
}

.success__top {
  color: #fff;
  position: relative;
  z-index: 2;

  .video__title {
    color: #fff;
  }

  .team__undertitle {
    max-width: 685px;
  }
}

.suxxess__bottom {
  margin-top: 84px;
  position: relative;
  z-index: 2;

  .advent__item {
    padding: 0;
  }
}

.suxxess__decor {
  width: 910px;
  opacity: 0.3;
  top: 50%;
  left: 50%;
  transform: translateX(-66%) translateY(-26%);
  position: absolute;

  img {
    width: 100%;
    height: auto;
  }
}


@media screen and (max-width: 1240px) {
  .suxxess__decor {
    width: 850px;
    transform: translateX(-70%) translateY(-74%);
  }
}


@media screen and (max-width: 980px) {
  .section__success {
    padding: 40px 0;
  }
  .suxxess__decor {
    width: 650px;
    top: 60px;
    transform: translateX(-70%) translateY(0);
  }
  .suxxess__bottom {
    margin-top: 40px;
  }
}


@media screen and (max-width: 767px) {
  .suxxess__decor {
    width: 610px;
    top: 60px;
    opacity: 0.3;
    transform: translateX(-70%) translateY(0);
  }
}