.viewOrder {
  .container {
    max-width: 1730px;
  }

  .account__side {
    width: 16.73%;
    min-height: 562px;
    position: relative;
  }

  .acount__main {
    width: 80.79%;
  }

  .accoun__title {
    display: none;
  }

  .account__content {
    padding-top: 14px;
    margin-top: 0;
  }

  .back__toview {
    display: none;
  }
}

.accountnav__inner {
  position: absolute;
  left: 34px;
  right: 34px;
  bottom: 28px;
  top: 28px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.record__to {
  font-size: 16px;
  line-height: 1.2;
  color: #43AE4E;
  font-weight: 600;
}

.record__ttle {
  margin-top: 6px;
  font-size: 21px;
  line-height: 1.2;
  font-weight: 600;
}

.record__main {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.record__video, .viev__additionalcont {
  width: 66.99%;
}

.record__videoinner {
  padding-top: calc(100% / 16 * 9);
  background-color: #000;
  position: relative;

  * {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}

.record__chatcont {
  width: 30.76%;
  padding-top: calc(66.99% / 16 * 9);
  .msgs {
    height: calc(100% - 141px) !important;
    overflow: auto;
  }

  .msgForm {
    height: 141px;

    .msgText {
      height: 28px;
    }
  }
}

.view__tab {
  display: none;

  &.active {
    display: block;
  }
}

.fullVideo {
  width: 100% !important;
}

.viewadd__part {
  padding-top: 40px;
}

.viewadd__title {
  font-size: 18px;
  line-height: 1.5;
  font-weight: 600;
}

.viewadd__textblock {
  margin-top: 12px;
  padding: 24px;
  padding-top: 16px;
  border: 1px solid #ECECEC;
}

.viewadd__textinner {
  font-size: 14px;
}

.viewadd__part {
  .library__list {
    margin: 0 -12px;
    margin-top: 16px;
    margin-bottom: -24px;
    justify-content: flex-start;

    .library__item {
      width: calc(100% / 3 - 24px);
      margin: 0 12px;
      margin-bottom: 24px;
      padding: 16px;
      padding-top: 16px;
      padding-right: 8px;

      .library__icon {
        width: 42px;
        min-width: 42px;

        .library__dockname {
          width: 42px;
          height: 22px;
          line-height: 22px;
          font-size: 12px;
          left: -7px;
          bottom: 9px;

          &.yt {
            width: 32px;
            height: 22px;
            background-position: left center;
          }

          &.kdm {
            -webkit-background-size: 34px;
            background-size: 34px;
          }
        }
      }

      .library__name {
        width: calc(100% - 42px);
        padding-left: 18px;
        font-size: 14px;
      }
    }
  }
}




@media screen and (max-width: 1690px) {
  .viewOrder {
    .account__side {
      width: 254px;
      padding-top: calc(((100% - 284px) / 100 * 66.99 / 16 * 9) + 70px);
      padding-bottom: 0;
      position: relative;
      min-height: inherit;
    }
    .acount__main {
      width: calc(100% - 284px);
    }
  }
  .viewadd__textblock {
    padding: 20px;
    padding-top: 12px;
  }
  .viewadd__part {
    .library__list {
      margin: 0 -10px;
      margin-top: 12px;
      margin-bottom: -20px;
      .library__item {
        width: calc(100% / 3 - 20px);
        margin: 0 10px;
        margin-bottom: 20px;
        .library__name {
          padding-left: 14px;
          font-size: 12px;
        }
      }
    }
  }
}


@media screen and (min-width: 1240px) and (max-width: 1728px) {
  .record__chatcont {
    .msgs {
      height: calc(100% - 8.16vw) !important;
    }
    .msgForm {
      height: 8.16vw;
      .msgText {
        height: 1.72vw;
      }
    }
  }
  .accountnav__inner {
    position: absolute;
    left: 34px;
    right: 34px;
    bottom: 28px;
    top: 28px;
  }
}



@media screen and (max-width: 1240px) {
  .viewOrder {
    .account__side {
      width: 28%;
      padding-top: calc(70% / 16 * 9 + 68px);
    }
    .acount__main {
      width: 70%;
    }
    .account__content {
      padding-top: 10px;
    }
  }
  .accountnav__inner {
    left: 22px;
    right: 22px;
    top: 22px;
    bottom: 22px;
  }
  .record__to {
    font-size: 14px;
  }
  .record__ttle {
    margin-top: 7px;
    font-size: 16px;
    line-height: 1.5;
  }
  .record__main {
    display: block;
  }
  .record__video, .viev__additionalcont {
    width: 100%;
  }
  .record__chatcont {
    margin-top: 20px;
    height: 500px;
    padding: 0;
    width: 100%;
  }
}


@media screen and (min-width: 981px) {
  .orderMobName {
    display: none !important;
  }
}

@media screen and (max-width: 980px) {
  .viewOrder {
    .account__side {
      width: 100%;
      padding: 26px;
      height: 379px;
      max-width: inherit;
      .accountnav__inner {
        position: static;
        height: 100%;
      }
    }
    .acount__main {
      width: 100%;
    }
    .account__content {
      padding-top: 26px;
    }
    .backto__acc {
      display: none;
    }
    .back__toview {
      display: block;
    }
  }
  .record__top {
    display: flex;
    flex-direction: column;
  }
  .record__to {
    order: 2;
    margin-top: 6px;
  }
  .record__ttle {
    margin: 0;
  }
  .record__main {
    margin-top: 34px;
  }
  .hideOnMob {
    display: none;
  }
  .noTab:not(.hideOnMob) {
    display: block;
  }
  .account__content {
    .woocommerce {
      flex-wrap: wrap;
    }
  }
  .orderMobName {
    width: 100%;
    font-size: 17px;
    line-height: 1.5;
    margin-bottom: 30px;
    font-weight: 600;
  }
  .section__account {
    &.noChosed {
      .acount__main {
        display: none;
      }
    }
  }
  .mustActive {
    display: block !important;
  }
  .disTabs {
    .noTab {
      display: none !important;
    }
    .acount__main {
      display: block !important;
    }
  }
  .viewadd__part {
    .library__list {
      .library__item {
        .library__icon {
          width: 42px;
          min-width: 42px;
          height: 56px;
          .library__dockname {
            top: auto;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 767px) {
  .record__chatcont {
    height: 327px;
    margin-top: 8px;
    .msgs {
      height: calc(100% - 109px) !important;
    }
    .msgForm {
      height: 109px;
    }
    .chat__send {
      bottom: 10px;
    }
    .chat__count {
      bottom: 25px;
    }
  }
  .viewadd__part {
    .library__list {
      .library__item {
        width: calc(50% - 24px);
        padding: 16px;
        padding-right: 8px;
        .library__itemtop {
          padding-left: 4px;
        }
        .library__icon {
          width: 25px;
          min-width: 25px;
          height: 28px;
          .library__dockname {
            width: 22px;
            height: 11px;
            line-height: 11px;
            font-size: 6px;
            left: -4px;
            bottom: 5px;
            &.yt {
              width: 16px;
              height: 12px;
            }
            &.kdm {
              -webkit-background-size: 20px;
              background-size: 20px;
            }
          }
        }
        .library__name {
          width: calc(100% - 25px);
        }
      }
    }
  }
  .viewadd__title {
    font-size: 14px;
  }
  .viewadd__textinner {
    font-size: 12px;
  }
}


@media screen and (max-width: 480px) {
  .viewadd__part {
    .library__list {
      .library__item {
        width: calc(100% - 24px);
      }
    }
  }
}