.section__ticket {
  padding: 80px 0;
  position: relative;
  overflow: hidden;
  background-attachment: fixed;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center top;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.parallax {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: auto;
}

.ticket__container {
  flex-direction: column;
  align-items: center;
  align-content: center;
  position: relative;
  z-index: 5;
}

.ticket__logo {
  width: 300px;

  img {
    width: 100%;
    height: auto;
  }
}

.ticket__main {
  margin-top: 60px;
  text-align: center;
}

.ticket__green {
  font-size: 22px;
  line-height: 1.23;
  color: #42ae4d;
  text-transform: uppercase;
}

.ticket__title {
  font-size: 38px;
  margin-top: 4px;
  line-height: 1.24;
  color: #fff;
  font-weight: 600;
}

.ticket__short {
  font-size: 20px;
  margin-top: 16px;
  line-height: 1.25;
  color: #fff;
}

.ticket__date {
  font-size: 36px;
  margin-top: 12px;
  line-height: 1.222;
  color: #fff;
  font-weight: 600;
}

.ticket__sumcont {
  margin-top: 46px;
}

.ticket__sumtext {
  font-size: 24px;
  line-height: 1.5;
  color: #fff;
  font-weight: 500;
}

.ticket__price {
  font-size: 36px;
  line-height: 1.5;
  color: #43c452;
  font-weight: 600;
}

.ticket__button {
  margin-top: 60px;
  max-width: 188px;
  width: 100%;
}

.quantity {
  display: none !important;
}

.button__bought {
  margin-top: 50px !important;
  background-color: #43c452 !important;
}

.bought__mess {
  margin-top: 10px;
  color: #fff;
}


@media screen and (max-width: 980px) {
  .section__ticket {
    padding: 60px 0;
  }
  .ticket__main {
    margin-top: 55px;
  }
  .ticket__logo {
    width: 260px;
  }
  .ticket__green {
    font-size: 20px;
  }
  .ticket__title {
    font-size: 34px;
  }
  .ticket__short {
    font-size: 18px;
  }
  .ticket__date {
    font-size: 32px;
  }
  .ticket__sumcont {
    margin-top: 40px;
  }
  .ticket__sumtext {
    font-size: 22px;
  }
  .ticket__price {
    font-size: 32px;
  }
  .ticket__button {
    margin-top: 40px;
  }
}


@media screen and (max-width: 767px) {
  .ticket__logo {
    width: 240px;
  }
  .ticket__main {
    margin-top: 40px;
  }
  .ticket__green {
    font-size: 20px;
  }
  .ticket__title {
    font-size: 30px;
  }
  .ticket__short {
    font-size: 18px;
  }
  .ticket__date {
    font-size: 32px;
  }
  .ticket__sumcont {
    margin-top: 30px;
  }
  .ticket__sumtext {
    font-size: 20px;
  }
  .ticket__price {
    font-size: 28px;
  }
  .ticket__button {
    margin-top: 40px;
  }
}


@media screen and (max-width: 480px) {
  .ticket__logo {
    width: 220px;
  }
  .ticket__main {
    margin-top: 30px;
  }
  .ticket__green {
    font-size: 18px;
  }
  .ticket__title {
    font-size: 26px;
  }
  .ticket__short {
    font-size: 16px;
  }
  .ticket__date {
    font-size: 22px;
  }
  .ticket__sumtext {
    font-size: 18px;
  }
  .ticket__price {
    font-size: 24px;
  }
  .ticket__button {
    margin-top: 30px;
  }
}