.section__map {
  background-color: #F2F2F2;
  display: flex;
  flex-wrap: wrap;
}

.map__part {
  min-height: 600px;
  width: 50%;
  padding: 90px;
  position: relative;

  &:last-child {
    padding: 0;
  }

  iframe {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.map__title {
  font-size: 32px;
  line-height: 1.15;
  color: #0c112e;
  font-weight: 600;
}

.map__contact {
  margin-top: 46px;
}

.map__address {
  font-size: 24px;
  line-height: 1.5;
  font-weight: 300;
  color: #0c112e;
  font-style: normal;
}

.map__text {
  margin-top: 32px;
  font-size: 18px;
  color: #0c112e;
  font-weight: 300;
}

.map__list {
  margin-top: 32px;
  li {
    a {
      font-size: 18px;
      color: #0c112e;
      font-weight: 300;
    }
  }
}

.map__soc {
  margin-top: 50px;

  li {
    margin-right: 4px;

    &:last-child {
      margin: 0;
    }

    a {
      display: block;

      img {
        width: 30px;
        height: auto;
        display: block;
      }
    }
  }
}


@media screen and (max-width: 1340px) {
  .map__part {
    padding: 90px calc((100vw - 1190px) / 2);
  }
}


@media screen and (max-width: 1220px) {
  .map__part {
    min-height: 500px;
    padding: 60px 15px;
  }
}


@media screen and (max-width: 980px) {
  .map__part {
    padding: 40px 15px;
  }
  .map__title {
    font-size: 28px;
  }
  .map__contact {
    margin-top: 30px;
  }
  .map__address {
    font-size: 22px;
  }
  .map__text {
    margin-top: 26px;
    font-size: 16px;
  }
  .map__list {
    margin-top: 26px;
    li {
      a {
        font-size: 16px;
      }
    }
  }
  .map__soc {
    margin-top: 40px;
  }
}


@media screen and (max-width: 767px) {
  .map__part {
    width: 100%;
    min-height: inherit;
    &:last-child {
      min-height: 300px;
      padding-top: 41.61%;
    }
  }
}