.section__abfirst {
  overflow: hidden;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 2;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.abfirst__container {
  padding: 190px 0;
  min-height: 100vh;
  position: relative;
  z-index: 10;
  flex-direction: column;
  justify-content: center;
}

.about__carousel {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  * {
    height: 100%;
  }
}

.abfirst__content {
  max-width: 710px;
  width: 100%;
  color: #fff;
}

.abfirst__title {
  font-size: 34px;
  line-height: 1.2;
  font-weight: 500;
}

.abfirst__text {
  margin-top: 40px;
  font-size: 24px;
  line-height: 1.5;
  font-weight: 300;
}


@media screen and (max-width: 1600px) {
  .abfirst__container {
    padding: 150px 0;
  }
}


@media screen and (max-width: 1400px) {
  .abfirst__container {
    padding: 140px 0;
  }
}


@media screen and (max-width: 1240px) {
  .abfirst__container {
    padding: 140px 0;
  }
}

@media screen and (max-width: 1140px) {
  .abfirst__container {
    padding: 100px 0;
  }
}


@media screen and (max-width: 980px) {
  .abfirst__container {
    padding: 100px 0;
  }
}


@media screen and (max-width: 767px) {
  .abfirst__title {
    font-size: 30px;
  }
  .abfirst__text {
    margin-top: 30px;
    font-size: 20px;
  }
}


@media screen and (max-width: 767px) {
  .abfirst__title {
    font-size: 28px;
  }
  .abfirst__text {
    font-size: 18px;
  }
}