.section__about {
  padding: 60px 0;
  background-color: #fff;
}

.avout__content {
  align-items: center;
  align-content: center;
  justify-content: space-between;
}

.about__images {
  width: 44.44%;
  position: relative;
}

.about__imagesin {
  padding-top: 77.69%;
}

.about__img {
  position: absolute;
  overflow: hidden;

  &.ing1 {
    left: 19.23%;
    width: 65.96%;
    bottom: 0;
    z-index: 3;

    .ab__imginner {
      padding-top: 62.1%;
    }
  }

  &.ing2 {
    left: 0;
    width: 57.69%;
    top: 7.92%;

    .ab__imginner {
      padding-top: 70%;
    }
  }

  &.ing3 {
    right: 0;
    top: 0;
    width: 38%;

    .ab__imginner {
      padding-top: 144.39%;
    }
  }
}

.about__info {
  width: 50.43%;
}

.abput__title {
  font-size: 42px;
  line-height: 1.2;
  color: $blue;
  font-weight: 700;
}

.abput__short {
  font-size: 18px;
  margin-top: 20px;
  color: $turquoise;
  font-weight: 600;
}

.about__text {
  margin-top: 10px;
  font-size: 18px;

  a {
    color: $red;
  }
}


@media screen and (max-width: 980px) {
  .section__about {
    padding: 40px 0;
  }
  .avout__content {
    max-width: 600px;
    width: 100%;
    margin: 0 auto;
    display: block;
  }
  .about__images {
    width: 100%;
  }
  .about__imagesin {
    padding-top: 79.86%;
  }
  .about__img {
    &.ing1 {
      left: 16.96%;
      width: 71.55%;
      .ab__imginner {
        padding-top: 67.9%;
      }
    }
    &.ing2 {
      width: 65.19%;
      top: 0;
      .ab__imginner {
        padding-top: 61.79%;
      }
    }
    &.ing3 {
      width: 31.45%;
      .ab__imginner {
        padding-top: 155.62%;
      }
    }
  }
  .about__info {
    margin-top: 30px;
    width: 100%;
  }
  .abput__title {
    font-size: 38px;
  }
  .abput__short {
    margin-top: 10px;
  }
}


@media screen and (max-width: 767px) {
  .abput__title {
    font-size: 30px;
  }
  .abput__short {
    font-size: 16px;
    br {
      display: none;
    }
  }
  .about__text {
    font-size: 16px;
  }
}


@media screen and (max-width: 480px) {
  .about__imagesin {
    margin-bottom: -2px;
    height: auto;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .about__img {
    margin-bottom: 2px;
    position: relative;
    left: inherit !important;
    right: inherit !important;
    top: inherit !important;
    bottom: inherit !important;
    &.ing1 {
      width: 100%;
      .ab__imginner {
        padding-top: 55.99%;
      }
    }
    &.ing2 {
      width: calc(60% - 1px);
      .ab__imginner {
        padding-top: 55.79%;
      }
    }
    &.ing3 {
      width: calc(40% - 1px);
      .ab__imginner {
        padding-top: 149.62%;
      }
    }
  }
  .abput__title {
    font-size: 28px;
  }
  .about__text {
    font-size: 14px;
  }
}