.section__first {
  padding-top: 190px;
  height: 100vh;
  max-height: 760px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.first__container {
  max-width: 742px;
  padding-top: 30px;
  padding-bottom: 120px;
  color: #fff;
  position: relative;
  z-index: 5;
}

.first__title {
  font-size: 40px;
  line-height: 1.2225;
  font-weight: 600;
}

.first__text {
  font-size: 22px;
  margin-top: 38px;
  color: #afafaf;
}

.first__button {
  margin-top: 44px;
  max-width: 378px;
  width: 100%;
}

.dirst__arrow {
  bottom: 50px;
  width: 39px;
  height: 19px;
  animation: arrow 1.5s ease-in-out infinite;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;

  img {
    width: 100%;
    height: 100%;
  }
}

@keyframes arrow {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(40%);
  }

  100% {
    transform: translateY(0);
  }
}

.first__video {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100vw;
  height: 100%;
  object-fit: cover;
  top: 50%;
}

.first__mobbg {
  display: none;
}



@media screen and (max-width: 1600px) {
  .section__first {
    padding-top: 150px;
  }
}


@media screen and (max-width: 1400px) {
  .section__first {
    padding-top: 140px;
  }
}


@media screen and (max-width: 1140px) {
  .section__first {
    padding-top: 90px;
  }
}

@media screen and (max-width: 980px) {
  .section__first {
    padding-top: 0;
    height: auto;
    max-height: inherit;
  }
  .first__container {
    padding: 220px 0;
  }
  .first__video {
    display: none;
  }
  .first__mobbg {
    display: block;
  }
}



@media screen and (max-height: 760px) {
  .section__first {
    height: auto;
    min-height: 100vh;
  }
}

@media screen and (max-width: 767px) {
  .section__first {
    min-height: inherit;
  }
  .first__container {
    padding: 100px 0;
    padding-bottom: 80px;
  }
  .first__title {
    font-size: 36px;
  }
  .first__text {
    margin-top: 24px;
    font-size: 18px;
  }
  .first__button {
    margin-top: 36px;
  }
  .dirst__arrow {
    bottom: 30px;
    width: 27px;
    height: 14px;
  }
}

@media screen and (max-width: 480px) {
  .first__title {
    font-size: 32px;
  }
  .first__text {
    font-size: 16px;
  }
  .first__button {
    span {
      font-size: 15px;
    }
  }
}
