.section__comment {
  padding: 60px 0;
}

.comment__content {
  max-width: 800px;
  margin: 0 auto;
  margin-top: 50px;
}

.comment-respond {
  .comment-reply-title {
    display: none;
  }
}

.must-log-in {
  min-height: 120px;
  padding: 46px 20px;
  background: #F0F0F0;
  border-radius: 10px;
  text-align: center;
  font-size: 16px;

  a {
    color: #43AE4E;
    text-decoration: underline;
  }
}

.comment-form {
  .comment-form-comment {
    margin-top: 16px;

    label {
      display: none;
    }

    #comment {
      border: 1px solid #808080;
      border-radius: 10px;
      height: 120px ;
      min-height: 120px ;
      padding: 18px;
      font-size: 15px;
      line-height: 1.21;
      width: 100%;
      display: block;
      max-width: 100%;
      min-width: 100%;
    }
  }

  .form-submit {
    margin-top: 24px;
    display: none;

    &.submitN {
      display: block;
    }

    #submit, .review__button {
      width: 185px;
      height: 52px !important;
      font-size: 15px !important;
      border-radius: 30px;
      background-color: #43AE4E !important;
      border-color: #43AE4E !important;
      color: #fff;
      font-weight: 700;
      cursor: pointer;

      &:hover {
        background-color: transparent !important;
        color: #000 !important;
      }
    }

    #submit {
      display: none;
    }
  }
}

.errorRev {
  width: 100%;
  font-size: 14px;
  color: $red;
  margin-top: 8px;
  display: none;
}

.error {
  .errorRev {
    display: block;
  }
}

.comment-form-rating {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;

  .errorRev {
    margin-top: 2px;
  }

  label {
    font-size: 15px;
    margin-right: 15px;
    span {
      display: none;
    }
  }
}

.stars {
  display: inline-block;
  padding: 0;
  margin: 0;
  & > span {
    display: flex;

    &.star-1 {
      .star-1 {
        background-image: url(../img/icons/star.svg);
      }
    }

    &.star-2 {
      .star-1 {
        background-image: url(../img/icons/star.svg);
      }
      .star-2 {
        background-image: url(../img/icons/star.svg);
      }
    }

    &.star-3 {
      .star-1 {
        background-image: url(../img/icons/star.svg);
      }
      .star-2 {
        background-image: url(../img/icons/star.svg);
      }
      .star-3 {
        background-image: url(../img/icons/star.svg);
      }
    }

    &.star-4 {
      .star-1 {
        background-image: url(../img/icons/star.svg);
      }
      .star-2 {
        background-image: url(../img/icons/star.svg);
      }
      .star-3 {
        background-image: url(../img/icons/star.svg);
      }
      .star-4 {
        background-image: url(../img/icons/star.svg);
      }
    }

    &.star-5 {
      * {
        background-image: url(../img/icons/star.svg);
      }
    }

    * {
      width: 38px;
      height: 32px;
      transition: .3s;
      color: transparent;
      display: block;
      background-repeat: no-repeat;
      background-position: center center;
      -webkit-background-size: 23px;
      background-size: 23px;
      background-image: url(../img/icons/star_u.svg);
      position: relative;
    }
  }
}

.woocommerce-noreviews {
  margin-top: 1em;
  font-size: 1.3em;
  font-weight: 600;
  text-align: center;
}

.commentlist {
  margin-top: 54px;
  padding: 0;

  .review {
    padding-bottom: 28px;
    margin-bottom: 36px;
    border-bottom: 1px solid #ECECEC;

    &:last-child {
      margin: 0;
      padding: 0;
      border: none;
    }

    .comment-text {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      align-content: center;
    }

    .star-rating {
      & > span {
        display: none;
      }
    }

    .stars {
      margin-right: 18px;
      display: block;

      span {
        span {
          width: 20px;
          height: 15px;
          line-height: 0;
          -webkit-background-size: contain;
          background-size: contain;
        }
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    .meta {
      min-width: calc(100% - 118px);
      display: flex;
      justify-content: space-between;
      align-items: center;

      .woocommerce-review__author {
        font-size: 16px;
        line-height: 1.2;
        font-weight: 700;
        color: #000;
        margin-right: 1em;

        a {
          color: #000;
        }
      }

      .woocommerce-review__dash {
        display: none;
      }

      .woocommerce-review__published-date {
        font-size: 13px;
        color: #808080;
      }
    }

    .description {
      margin-top: 24px;
      font-size: 15px;
    }
  }
}

.wdpajax-error {
  margin-top: .5em;
}

.alert.alert-warning {
  margin-top: .5em;
  color: $red;
}


@media screen and (max-width: 980px) {
  .section__comment {
    padding: 40px 0;
  }
}


@media screen and (max-width: 767px) {
  .comment__content {
    margin-top: 40px;
  }
  .must-log-in {
    min-height: 80px;
    padding: 24px 15px;
    font-size: 14px;
  }
  .comment-form {
    .comment-form-comment {
      margin-top: 16px;
      #comment {
        height: 140px ;
        min-height: 140px ;
        padding: 14px;
        font-size: 14px;
      }
    }
    .form-submit {
      margin-top: 16px;
      justify-content: center;
      &.submitN {
        display: flex;
      }
      #submit, .review__button {
        width: 155px;
        height: 48px !important;
        font-size: 14px !important;
      }
    }
  }
  .errorRev {
    font-size: 12px;
    margin-top: 4px;
  }
  .stars {
    display: flex;
    justify-content: center;
    & > span {
      * {
        width: 28px;
        height: 26px;
      }
    }
  }
  .commentlist {
    .review {
      padding-bottom: 24px;
      margin-bottom: 24px;
      .meta {
        .woocommerce-review__published-date {
          font-size: 12px;
        }
      }
      .description {

        margin-top: 16px;
        font-size: 14px;
      }
    }
  }
  .wdpajax-error {
    text-align: center;
  }
  .alert.alert-warning {
    text-align: center;
  }
}


@media screen and (max-width: 480px) {
  .meta {
    margin-top: 14px;
    width: 100%;
  }
}