.trans__top {
  padding: 0 20px;
  height: 55px;
  border-bottom: 1px solid #ECECEC;
  background-color: #fff;
  align-items: center;
  align-content: center;
}

.trans__bacl {
  padding-left: 38px;
  position: relative;
  color: #000;

  &::before {
    width: 28px;
    height: 28px;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
