.translation__main {
  margin-top: 103px;
  padding-bottom: 109px;

  .container {
    max-width: 1728px;
  }
}

.trans__toptitle {
  font-size: 16px;
  line-height: 1.25;
  font-weight: 600;
  color: #43AE4E;
}

.trans__title {
  margin-top: 7px;
  font-size: 21px;
  line-height: 1.23;
  font-weight: 600;
  color: #000;
}

.trans__body {
  margin-top: 30px;
  justify-content: space-between;
}

.trans__videocont {
  width: 73.3%;
}

.transvideo__inner {
  padding-top: calc(100% / 16 * 9);
  background-color: #000;
  position: relative;

  * {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}

.trans__chatcont {
  width: 24.88%;
  padding-top: calc(73.3% / 16 * 9);
  background-color: #F9F9F9;
  border: 1px solid #ECECEC;
  position: relative;

  .chat-outer {
    margin: 0 !important;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
  }

  .chatWindow {
    height: 100%;
  }

  .chatbox {
    padding: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;

    .chat {
      height: 100%;
      background-color: transparent;
    }
  }

  .msgs {
    padding: 0 26px;
    height: calc(100% - 189px) !important;
    border: none !important;

    .msgRow {
      padding: 19px 0;
      border-bottom: 1px solid #ECECEC;

      .chat_name {
        font-size: 14px;
        line-height: 1.21;
        font-weight: 500;
        color: #808080;
      }

      .chat_text {
        margin-top: 4px;
        font-size: 13px;
        line-height: 1.69;
        font-weight: 400;
      }
    }
  }
}

.msgs::-webkit-scrollbar-button, .msgText::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  background-color: #CCCCCC;
}
.msgs::-webkit-scrollbar-track, .msgText::-webkit-scrollbar-track {
  background-color: #F1F1F1;
}
.msgs::-webkit-scrollbar-thumb, .msgText::-webkit-scrollbar-thumb {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #CCCCCC;
}
.msgs::-webkit-scrollbar-thumb:hover, .msgText::-webkit-scrollbar-thumb:hover {
  background-color: #CCCCCC;
}
.msgs::-webkit-resizer, .msgText::-webkit-resizer {
  width: 4px;
}
.msgs::-webkit-scrollbar, .msgText::-webkit-scrollbar{
  width: 4px;
}

.msgForm {
  height: 189px;
  padding: 0 26px;
  padding-top: 20px !important;
  width: 100%;
  background-color: #fff;
  position: relative;

  .msgText {
    height: 78px;
    margin-top: 16px;
    font-size: 14px;
    line-height: 1.64;
    border-bottom: 1px solid #43AE4E;
    display: block;
    width: 100%;
    resize: none;
  }
}

.chat__auth {
  font-size: 14px;
  line-height: 1.21;
  font-weight: 600;
  color: #1A325C;
}

.chat__send {
  bottom: 11px;
  width: 36px;
  height: 36px;
  -webkit-background-size: 26px;
  background-size: 26px;
  right: 26px;
  background-repeat: no-repeat;
  background-position: center right;
  position: absolute;
  background-color: transparent;
  border: none;
  font-size: 0;
  cursor: pointer;
  background-image: url("data:image/svg+xml,%0A%3Csvg width='26' height='24' viewBox='0 0 26 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M2.26285 0.871441C1.93079 0.71813 1.54099 0.765567 1.2543 0.991752C0.967616 1.21794 0.831492 1.58643 0.903679 1.94462L2.92491 12.0975L0.882367 22.181C0.809492 22.5385 0.943555 22.9063 1.22886 23.1339C1.51417 23.3614 1.90261 23.4102 2.23535 23.2604L24.8732 13.0305C25.2196 12.8737 25.4438 12.5286 25.4451 12.1477C25.4465 11.7668 25.2252 11.4203 24.8787 11.2609L2.26285 0.871441ZM4.72202 13.0243L3.16553 20.7063L22.128 12.1374L3.18203 3.43373L4.70552 11.08L10.7335 11.0539C11.2697 11.0512 11.707 11.4857 11.7097 12.0219C11.7118 12.5588 11.2786 12.9961 10.7417 12.9981L4.72202 13.0243Z' fill='%2343AE4E'/%3E%3C/svg%3E%0A");

  &:disabled {
    opacity: .5;
    cursor: inherit;
  }
}

.chat__count {
  bottom: 29px;
  right: 64px;
  font-size: 14px;
  line-height: 1.14;
  transform: translateY(50%);
  position: absolute;
}

.translation__empty {
  min-height: calc(100vh - 267px);
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.tre__top {
  font-size: 16px;
  line-height: 1.2;
  color: #43AE4E;
  font-weight: 600;
}

.tre__title {
  margin-top: 12px;
  font-size: 21px;
  line-height: 1.2;
  font-weight: 600;
}

.transem__btn {
  margin-top: 40px;
  width: 182px;
  height: 45px !important;
}

.tre__bottom {
  margin-top: 40px;
  font-size: 14px;
  color: #808080;

  &.less__emt {
    margin-top: 25px;
  }
}


@media screen and (min-width: 1240px) and (max-width: 1728px) {
  .trans__chatcont {
    .msgs {
      padding: 0 1.5vw;
      height: calc(100% - 10.94vw) !important;
      .msgRow {
        padding: 1.1vw 0;
        .chat_name {
          font-size: 0.81vw;
        }

        .chat_text {
          margin-top: 0.23vw;
          font-size: 0.75vw;
        }
      }
    }
  }
  .msgs::-webkit-scrollbar-thumb, .msgText::-webkit-scrollbar-thumb {
    -webkit-border-radius: 0.29vw;
    border-radius: 0.29vw;
  }
  .msgs::-webkit-resizer, .msgText::-webkit-resizer {
    width: 0.23vw;
  }
  .msgs::-webkit-scrollbar, .msgText::-webkit-scrollbar{
    width: 0.23vw;
  }
  .msgForm {
    height: 10.94vw;
    padding: 0 1.5vw;
    padding-top: 1.16vw !important;
    .msgText {
      height: 4.51vw;
      margin-top: 0.93vw;
      font-size: 0.81vw;
    }
  }
  .chat__auth {
    font-size: 0.81vw;
  }
  .chat__send {
    bottom: 0.64vw;
    width: 2.08vw;
    height: 2.08vw;
    -webkit-background-size: 1.5vw;
    background-size: 1.5vw;
    right: 1.5vw;
  }
  .chat__count {
    bottom: 1.68vw;
    right: 3.7vw;
    font-size: 0.81vw;
  }
}



@media screen and (max-width: 1240px) {
  .trans__top {
    padding: 0 15px;
  }
  .translation__main {
    margin-top: 80px;
    padding-bottom: 80px;
  }
  .trans__videocont {
    width: 100%;
  }
  .trans__chatcont {
    margin-top: 20px;
    height: 600px;
    padding: 0;
    width: 100%;
  }
  .translation__empty {
    min-height: calc(100vh - 215px);
  }
}



@media screen and (max-width: 980px) {
  .trans__top {
    height: 42px;
  }
  .translation__main {
    margin-top: 60px;
    padding-bottom: 60px;
  }
  .translation__empty {
    min-height: calc(100vh - 162px);
  }
}


