.footer {
  background-color: #000712;
}

.footer__top {
  padding: 60px 0;
  justify-content: center;
}

.footer__main {
  justify-content: space-between;
  align-items: center;
  align-content: center;

  .custom-logo-link {
    width: 210px;
  }

  .header__soc {
    li {
      margin-right: 12px;

      &:last-child {
        margin: 0;
      }

      a {
        img {
          width: 40px;
          height: 40px;
        }
      }
    }
  }
}

.footer__contact {
  li {
    a {
      font-size: 20px;
      line-height: 1.5;
      color: #fff;
    }
  }
}

.footer__bottom {
  margin-top: 40px;
  padding: 20px 0;
  font-size: 14px;
  line-height: 1.42;
  font-weight: 300;
  justify-content: space-between;
}

.copyright {
  color: #b8b8b8;
}

.footer__nav {
  li {
    margin-right: 50px;

    &:last-child {
      margin: 0;
    }

    a {
      color: #b8b8b8;
    }
  }
}

.to__top {
  width: 50px;
  height: 50px;
  left: 30px;
  bottom: 45px;
  position: fixed;
  z-index: 80;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: contain;
  background-size: contain;
  background-image: url(../img/icons/top.svg);
  display: none;
}

.sp-form-outer {
  justify-content: center;
  align-items: center;
  align-content: center;
  overflow: hidden !important;

  &:not(.sp-hide):not(.sp-force-hide) {
    display: flex !important;
  }

  .sp-form {
    padding: 0 !important;
    max-width: calc(100% - 30px) !important;
    margin: 0 !important;

    .sp-form-fields-wrapper {
      padding: 15px;
      max-height: calc(100vh - 30px);
      margin: 0 !important;
      overflow: auto;
      width: 100% !important;
    }
  }
}

.translation__btn {
  position: fixed;
  right: 60px;
  bottom: 60px;
  width: 297px;
  height: 60px;
  display: block;
  color: #fff;
  z-index: 81;

  & > span {
    background-repeat: no-repeat;
    background-position: 38px center;
    -webkit-background-size: 19px;
    background-size: 19px;
    border-radius: 60px;
    padding-left: 78px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: #43AE4E;
    position: relative;
    z-index: 2;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='20' height='24' viewBox='0 0 20 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18.7422 10.6465L2.42969 0.9375C2.23047 0.820312 2.02539 0.75 1.79102 0.75C1.15234 0.75 0.630859 1.27734 0.630859 1.92188H0.625V22.0781H0.630859C0.630859 22.7227 1.15234 23.25 1.79102 23.25C2.03125 23.25 2.23047 23.168 2.44727 23.0508L18.7422 13.3535C19.1289 13.0312 19.375 12.5449 19.375 12C19.375 11.4551 19.1289 10.9746 18.7422 10.6465Z' fill='white'/%3E%3C/svg%3E%0A");

    span {
      margin-top: 4px;
      font-size: 16px;
      line-height: 20px;
      font-weight: 600;
      &:first-child {
        font-size: 14px;
        line-height: 17px;
        margin: 0;
        font-weight: 400;
      }
    }
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    border-radius: 100px;
    animation: fill 1.5s ease-out infinite;
  }

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    border-radius: 100px;
    background: rgba(67, 174, 78, 0.2);
    animation: fill 1.5s .5s ease-out infinite;
  }
}

@keyframes fill {
  0% {
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(67, 174, 78, 0.5);
  }
  70% {
    left: -25px;
    top: -25px;
    bottom: -25px;
    right: -25px;
    background: rgba(67, 174, 78, 0);
  }
  100% {
    left: -25px;
    top: -25px;
    bottom: -25px;
    right: -25px;
    background: rgba(67, 174, 78, 0);
  }
}


@media screen and (max-width: 1240px) {
  .footer__main {
    .custom-logo-link {
      width: 170px;
    }
  }
  .footer__nav {
    li {
      margin-right: 30px;
    }
  }
  .to__top {
    width: 40px;
    height: 40px;
    left: 15px;
    bottom: 65px;
  }
}


@media screen and (max-width: 980px) {
  .footer__top {
    padding: 40px 0;
  }
  .footer__main {
    .header__soc {
      display: flex !important;
      position: static !important;
      li {
        margin-right: 10px;
        a {
          img {
            width: 30px;
            height: 30px;
          }
        }
      }
    }
  }
  .footer__contact {
    li {
      a {
        font-size: 16px;
      }
    }
  }
  .footer__bottom {
    margin-top: 20px;
    font-size: 12px;
  }
  .to__top {
    bottom: 60px;
  }
}


@media screen and (max-width: 767px) {
  .footer__main {
    justify-content: space-around;
    .footer__part:first-child {
      width: 100%;
      display: flex;
      justify-content: center;
      margin-bottom: 40px;
    }
  }
  .copyright {
    width: 100%;
    text-align: center;
  }
  .footer__nav {
    width: 100%;
    text-align: center;
    margin-top: 40px;
    padding: 0 50px;
    justify-content: center;
    li {
      margin: 0 10px;
      margin-bottom: 10px;
    }
  }
  .to__top {
    bottom: 15px;
  }
  .translation__btn {
    right: 30px;
    bottom: 30px;
    width: 267px;
    height: 56px;
    & > span {
      background-position: 24px center;
      padding-left: 60px;
    }
  }
}


@media screen and (max-width: 767px) {
  .footer__main {
    display: block;
    .footer__part {
      width: 100%;
      margin-bottom: 40px;
      display: flex;
      justify-content: center;
      text-align: center;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}


@media screen and (max-width: 480px) {
  .footer__top {
    .button {
      height: 48px;
      font-size: 3.5vw !important;
    }
  }
  .footer__main {
    .footer__part {
      margin-bottom: 20px !important;
      &:last-child {
        margin-bottom: 0 !important;
      }
    }
  }
  .footer__nav {
    margin-top: 20px;
    li {
      margin-bottom: 5px;
    }
  }
  .translation__btn {
    right: 15px;
    bottom: 15px;
  }
}