.section__checkout {
  position: relative;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover;
  background-size: cover;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.checkout_content {
  min-height: 100vh;
  padding: 110px 0;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.checkout__top {
  min-height: 55px;
  padding: 10px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  align-content: center;
}

.woocommerce-NoticeGroup {
  margin-bottom: 1em;

  .woocommerce-error {
    * {
      font-size: 14px;
      color: $red;
    }
  }
}

.checkout__back {
  position: relative;
  padding-left: 28px;
  font-size: 14px;
  color: #fff;

  &::before {
    width: 9px;
    height: 16px;
    content: '';
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='10' height='16' viewBox='0 0 10 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.37519 1.00002C9.37586 1.11518 9.35378 1.22933 9.31024 1.33594C9.2667 1.44255 9.20254 1.53951 9.12144 1.62127L2.73394 8.00002L9.12144 14.3788C9.28621 14.5435 9.37877 14.767 9.37877 15C9.37877 15.233 9.28621 15.4565 9.12144 15.6213C8.95667 15.786 8.7332 15.8786 8.50019 15.8786C8.26718 15.8786 8.04371 15.786 7.87894 15.6213L0.87894 8.62127C0.796928 8.53993 0.731833 8.44315 0.687411 8.33653C0.642988 8.2299 0.620117 8.11553 0.620117 8.00002C0.620117 7.88451 0.642988 7.77014 0.687411 7.66352C0.731833 7.55689 0.796928 7.46012 0.87894 7.37877L7.87894 0.378773C7.96028 0.296761 8.05706 0.231665 8.16369 0.187242C8.27031 0.14282 8.38468 0.11995 8.50019 0.11995C8.6157 0.11995 8.73007 0.14282 8.83669 0.187242C8.94332 0.231665 9.0401 0.296761 9.12144 0.378773C9.20254 0.460534 9.2667 0.557497 9.31024 0.664104C9.35378 0.770712 9.37586 0.884867 9.37519 1.00002Z' fill='white'/%3E%3C/svg%3E%0A");
  }
}

.checkout__main {
  max-width: 1160px;
  width: 100%;
}

.checkout__title {
  font-size: 42px;
  line-height: 1.3;
  color: #fff;
  font-weight: 600;
}

.checkout__mcont {
  margin-top: 54px;
  padding: 60px;
  padding-bottom: 65px;
  background-color: #fff;

  .section__error {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 200;
    .container {
      overflow: auto;
      height: 100%;
    }
  }
}

.chack__mintitle {
  font-size: 21px;
  font-weight: 600;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 9px;
    height: 1px;
    background-color: #ECECEC;
  }

  span {
    padding-right: 14px;
    position: relative;
    display: inline-block;
    background-color: #fff;
    z-index: 2;
  }
}

.woocommerce-billing-fields__field-wrapper {
  margin-top: 42px;
  margin-bottom: -38px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;

  .form-row {
    width: calc(50% - 19px);
    margin-bottom: 38px;

    .woocommerce-input-wrapper {
      display: block;

      input {
        height: 55px;
        border: 1px solid #D2D2D2;
        border-radius: 5px;
        padding: 0 25px;
        font-size: 15px;
        display: block;
        width: 100%;
      }
    }
  }
}

.checkout__review {
  margin-top: 60px;
}

#order_review_heading {
  font-size: 21px;
  font-weight: 600;
}

.woocommerce-checkout-review-order-table {
  margin-top: 42px;
}

.checkout__products {
  padding-bottom: 50px;
  border-bottom: 1px solid #ECECEC;
}

.checkout__product {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ECECEC;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;

  &:last-child {
    margin: 0;
    padding: 0;
    border: none;
  }
}

.product__left {
  min-width: 527px;
  width: 527px;
  margin-right: 30px;
  display: flex;
}

.chorid__img {
  margin-right: 22px;
  max-width: 102px;
  height: 85px;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.chprod__namecpnt {
  .chprod__title {
    text-transform: uppercase;
  }
}

.chprod__title {
  font-size: 14px;
  line-height: 1.2;
  color: #808080;
  margin-bottom: 6px;
}

.chprod__name {
  font-size: 15px;
  line-height: 1.7;
  font-weight: 500;
}

.chprod__middle {
  min-width: 150px;
  margin-right: 30px;
}

.chprod__date, .chprod__price {
  font-size: 15px;
  line-height: 1.7;
}

.cart-discount.coupon-test {
  a {
    display: none !important;
  }
}

.wc_payment_methods, .woocommerce-terms-and-conditions-wrapper {
  display: none !important;
}

#payment {
  display: none;
}

.place-order {
  .button.wp-element-button {
    height: 52px;
    width: 282px;
    background-color: #43AE4E;
    border-color: #43AE4E;
    color: #fff;
    
    &:hover {
      background-color: transparent;
      color: #000;
    }
  }
}

.chackout__bottom {
  margin-top: 46px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.ch__price {
  display: flex;
  margin-bottom: 4px;
  line-height: 1.2;

  &:last-child {
    margin: 0;
  }

  div:first-child {
    margin-right: 4px;
  }

  &.order-total {
    font-size: 18px;
    font-weight: 600;

    * {
      font-weight: 600;
    }
  }

  &.cart-discount {
    font-size: 14px;
  }

  &.cart-subtotal {
    font-size: 14px;
    color: #808080;
  }
}

.cart__mounce {
  .cart-empty.woocommerce-info {
    display: none;
  }

  .cartempty__buttons {
    margin-top: 20px;

    .button {
      margin-right: 15px;
    }
  }

  .woocommerce-notices-wrapper {
    .woocommerce-info {
      display: none;
    }

    .woocommerce-error {
      margin-bottom: 30px;
      color: $red;
    }
  }
}

.ifnotreg {
  font-size: 12px;
  color: #7F7F7F;

  a {
    color: #0693e3;
    text-decoration: underline;
  }
}

.needAuthText {
  font-size: 14px;
  line-height: 1.8;
  text-align: center;
  width: 100%;
}

.needAuthTitle {
  font-size: 17px;
  line-height: 1.7;
  color: #43AE4E;
  font-weight: 600;
  text-align: center;
  width: 100%;
}

.marginLess {
  margin-top: 30px !important;
}



@media screen and (max-width: 1240px) {
  .checkout_content {
    padding: 90px 0;
  }
  .checkout__top {
    min-height: 46px;
    padding: 8px 15px;
  }
  .checkout__title {
    font-size: 34px;
  }
  .checkout__mcont {
    margin-top: 40px;
    padding: 30px;
  }
  .chack__mintitle {
    font-size: 18px;
    &::before {
      bottom: 7px;
    }
    span {
      padding-right: 10px;
    }
  }
  .woocommerce-billing-fields__field-wrapper {
    margin-top: 24px;
    margin-bottom: -20px;
    .form-row {
      width: calc(50% - 10px);
      margin-bottom: 20px;
      .woocommerce-input-wrapper {
        input {
          height: 45px;
          border-radius: 3px;
          padding: 0 15px;
          font-size: 14px;
        }
      }
    }
  }
  .checkout__review {
    margin-top: 40px;
  }
  #order_review_heading {
    font-size: 18px;
  }
  .woocommerce-checkout-review-order-table {
    margin-top: 24px;
  }
  .checkout__products {
    padding-bottom: 32px;
  }
  .checkout__product {
    margin-bottom: 10px;
    padding-bottom: 10px;
  }
  .product__left {
    min-width: 500px;
    width: 500px;
    margin-right: 15px;
  }
  .chorid__img {
    margin-right: 15px;
    max-width: 90px;
    height: 80px;
  }
  .chprod__name {
    font-size: 14px;
  }
  .chprod__middle {
    min-width: 130px;
    margin-right: 15px;
  }
  .chprod__date, .chprod__price {
    font-size: 14px;
  }
  .chackout__bottom {
    margin-top: 40px;
  }
}


@media screen and (max-width: 980px) {
  .product__left {
    min-width: 400px;
    width: 400px;
  }
}


@media screen and (max-width: 767px) {
  .checkout_content {
    padding-top: 105px;
    padding-bottom: 40px;
  }
  .checkout__top {
    min-height: 55px;
  }
  .checkout__title {
    font-size: 21px;
  }
  .checkout__mcont {
    margin-top: 16px;
    padding: 38px 20px;
  }
  .chack__mintitle {
    font-size: 12px;
    &::before {
      bottom: 6px;
    }
    span {
      padding-right: 8px;
    }
  }
  .woocommerce-billing-fields__field-wrapper {
    margin-top: 20px;
    .form-row {
      width: 100%;
      .woocommerce-input-wrapper {
        input {
          font-size: 12px;
        }
      }
    }
  }
  .checkout__review {
    margin-top: 34px;
  }
  #order_review_heading {
    font-size: 12px;
  }
  .checkout__products {
    padding-bottom: 28px;
  }
  .checkout__product {
    flex-wrap: wrap;
  }
  .product__left {
    min-width: 100%;
    width: 100%;
    margin-right: 0;
    margin-bottom: 22px;
  }
  .chorid__img {
    margin-right: 14px;
    max-width: 58px;
    height: 48px;
  }
  .chprod__title {
    font-size: 10px;
    margin-bottom: 5px;
  }
  .chprod__name {
    font-size: 12px;
    line-height: 1.5;
  }
  .chprod__middle {
    min-width: inherit;
    margin-right: 0;
  }
  .chprod__date, .chprod__price {
    font-size: 12px;
  }
  .place-order {
    margin-top: 10px;
    width: 100%;
    .button.wp-element-button {
      max-width: 282px;
      width: 100%;
    }
  }
  .chackout__bottom {
    flex-direction: column;
    align-items: center;
    align-content: center;
  }
  .ch__price {
    justify-content: center;
    &.order-total {
      font-size: 14px;
    }
    &.cart-discount {
      font-size: 12px;
    }
    &.cart-subtotal {
      font-size: 12px;
    }
  }
  .cart__mounce {
    .cart-empty.woocommerce-info {
      display: none;
    }

    .cartempty__buttons {
      margin-top: 20px;

      .button {
        margin: 0;
        width: 100%;
        margin-bottom: 15px;

        &:last-child {
          margin: 0;
        }
      }
    }
  }
  .needAuthText {
    font-size: 12px;
  }

  .needAuthTitle {
    font-size: 14px;
  }
}


@media screen and (max-width: 368px) {
  .checkout__mcont {
    padding: 20px 15px;
    padding-bottom: 32px;
  }
  .chackout__bottom {
    margin-top: 24px;
  }
}