.section__program {
  padding: 60px 0;
  background-color: #F0F0F0;
}

.program__list {
  margin: 0 -20px;
  margin-top: 40px;
  margin-bottom: -40px;
}

.program__item {
  width: calc(100% / 3 - 40px);
  margin: 0 20px;
  margin-bottom: 40px;
  border-radius: 10px;
  padding: 30px;
  padding-bottom: 50px;
  background-color: #fff;
}

.program__time {
  font-size: 18px;
  line-height: 1.333;
  color: #42ae4d;
  font-weight: 600;
}

.program__name {
  margin-top: 26px;
  font-size: 22px;
  line-height: 1.36;
  font-weight: 600;
}

.program__text {
  margin-top: 26px;
  font-size: 18px;
  font-weight: 300;
}

.program__bottom {
  margin-top: 50px;
  justify-content: center;
}

.program__button {
  max-width: 520px;
  width: 100%;
}


@media screen and (max-width: 1240px) {
  .program__list {
    margin: 0 -10px;
    margin-top: 30px;
    margin-bottom: -20px;
  }
  .program__item {
    width: calc(100% / 3 - 20px);
    margin: 0 10px;
    margin-bottom: 20px;
  }
  .program__time {
    font-size: 16px;
  }
  .program__name {
    font-size: 20px;
  }
  .program__text {
    font-size: 16px;
  }
  .program__bottom {
    margin-top: 40px;
  }
}

@media screen and (max-width: 980px) {
  .section__program {
    padding: 40px 0;
  }

  .program__item {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 767px) {
  .program__item {
    padding: 15px;
    padding-bottom: 20px;
  }
  .program__time {
    font-size: 14px;
  }
  .program__name {
    font-size: 18px;
  }
  .program__text {
    font-size: 14px;
  }
}


@media screen and (max-width: 480px) {
  .program__list {
    margin-left: 0;
    margin-right: 0;
  }
  .program__item {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}