.popup {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 120;
  background-color: #fff;
  display: none;

  .popup__cont {
    transition: .3s;
  }

  &:not(.opened) {
    .popup__cont {
      transform: translateY(100px);
      opacity: 0;
    }
  }
}

.overlay {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: #000;
  opacity: .5;
}

.popup__cont {
  background-color: #fff;
  &.startAnim {
    opacity: 1;
    transition: 1s;
    &:not(.startDone) {
      opacity: 0;
    }
  }
}

.newsp__top {
  height: 56px;
  font-size: 14px;
  align-items: center;
  align-content: center;
  justify-content: center;
  font-weight: 600;
  border-bottom: 1px solid rgba(0,0,0,.2);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  background-color: #fff;
}

.newsp__back {
  width: 11px;
  height: 20px;
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: contain;
  background-size: contain;
  background-image: url("data:image/svg+xml,%3Csvg class='t-popup__close-icon' width='11' height='20' viewBox='0 0 11 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L10 10L1 19' stroke='%23000000' stroke-width='2'/%3E%3C/svg%3E");
}

.news__share {
  width: 20px;
  height: 18px;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: contain;
  background-size: contain;
  background-image: url(../img/icons/share.svg);

  &:hover {
    .share__list {
      opacity: 1;
      visibility: visible;
    }
  }

  .cur {
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
}

.share__list {
  position: absolute;
  top: calc(100% + 8px);
  right: -2px;
  z-index: 15;
  visibility: hidden;
  padding: 8px 4px;
  background-color: #fff;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, .3);
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
  opacity: 0;
  transition: .3s;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    height: 8px;
    top: 0;
    transform: translateY(-100%);
  }

  .heateor_sss_sharing_container {
    & > div, & > li {
      display: flex;
      align-items: center;
      align-content: center;

      a {
        display: block !important;
        width: 30px;
        height: 30px;
        margin: 0 4px !important;

        &:last-child {
          display: none !important;
        }

        span {
          width: 30px !important;
          height: 30px !important;
          display: flex !important;
          border-radius: 50%;
          margin: 0;
          align-items: center;
          align-content: center;
          justify-content: center;
        }

        svg {
          display: block;
          width: 27px;
          height: 27px;
          border-radius: 50%;
          margin: 0 !important;
        }
      }

      a.heateor_sss_more {
        display: none !important;
        opacity: 0;
        visibility: hidden;
        width: 0;
        height: 0;
        overflow: hidden;
      }
    }
  }
}

.newsp__inner {
  padding-top: 56px;
  overflow: auto;
  max-height: 100%;
}

.newsp__content {
  padding-top: 30px;
  padding-bottom: 60px;
  max-width: 760px;
  margin: 0 auto;
}

.newsp__title {
  font-size: 36px;
  line-height: 1.222;
  font-weight: 600;
}

.newsp__author {
  margin-top: 40px;
  align-items: center;
  align-content: center;
  width: 100%;
}

.newsp__authorava {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
}

.newsp__authorname {
  width: calc(100% - 30px);
  padding-left: 10px;
  font-size: 14px;
  font-weight: 300;
}

.newsp__op {
  margin-top: 40px;
  font-size: 10px;
  opacity: .5;
  font-weight: 600;
}

.newsp__img {
  margin-top: 30px;

  img {
    width: 100%;
    height: auto;
  }
}

.newsp__text {
  margin-top: 24px;

  iframe {
    width: 100%;
    height: calc(780px / 16 * 9);
  }
}


