#gal__popup {
  .popup__content {
    transform: none;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: transparent;
    position: absolute;
  }

  .popup__close {
    width: 32px;
    height: 32px;
    -webkit-background-size: 70%;
    background-size: 70%;
    background-color: rgba(0, 0, 0, .2);
    background-image: url("data:image/svg+xml,%0A%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' fill='%23fff' y='0px' viewBox='0 0 371.23 371.23' style='enable-background:new 0 0 371.23 371.23;' xml:space='preserve'%3E%3Cpolygon points='371.23,21.213 350.018,0 185.615,164.402 21.213,0 0,21.213 164.402,185.615 0,350.018 21.213,371.23 185.615,206.828 350.018,371.23 371.23,350.018 206.828,185.615 '/%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3Cg%3E%3C/g%3E%3C/svg%3E%0A");
  }
}

.gallery__carousel {
  width: 100%;
  height: 100%;

  .owl-stage-outer, .owl-stage, .owl-item {
    height: 100%;
  }

  .owl-nav {
    div {
      width: 50px;
      position: absolute;
      top: 0;
      bottom: 0;
      transition: .3s;
      z-index: 1;
      font-size: 0;
      opacity: 1;

      &.disabled {
        display: none;
      }

      &::before {
        width: 20px;
        height: 20px;
        border-top: 2px solid #000;
        border-right: 2px solid #000;
        content: '';
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateY(-50%) translateX(-50%) rotate(45deg);
      }

      &.owl-prev {
        left: 0;
        &::before {
          transform: translateY(-50%) translateX(-50%) rotate(220deg);
        }
      }

      &.owl-next {
        right: 0;
      }
    }
  }
}

.popGalItem {
  padding: 15px;
  height: 100%;
  width: 100%;
  overflow: auto;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;

  &.zoomedIt {
    display: block;
  }

  img {
    max-width: 100%;
    max-height: 100%;
    display: block;
    margin: 0 auto;
    position: relative;
    z-index: 5;
    cursor: zoom-in;

    &.zoomed {
      width: 100%;
      height: auto;
      max-height: inherit;
      cursor: zoom-out;
    }
  }

  .overlay {
    opacity: 0;
  }
}



@media screen and (max-width: 1240px) {
  .newsp__back {
    left: 15px;
  }
  .news__share {
    right: 15px;
  }
}


@media screen and (max-width: 980px) {
  .newsp__title {
    font-size: 28px;
  }
  .newsp__img {
    margin-top: 20px;
  }
  .newsp__text {
    margin-top: 18px;
  }
}


@media screen and (max-width: 767px) {
  .newsp__top {
    height: 50px;
    font-size: 12px;
  }
}