.section__team {
  padding: 60px 0;
}

.team__undertitle {
  margin: 0 auto;
  margin-top: 14px;
  font-size: 17px;
  max-width: 580px;
  width: 100%;
  opacity: 0.6;
  text-align: center;
}

.team__list {
  margin: 0 -18px;
  margin-top: 30px;
  margin-bottom: -36px;
}

.team__item {
  width: calc(25% - 36px);
  margin: 0 18px;
  margin-bottom: 36px;

  &:first-child {
    &:last-child {
      width: calc(100% - 36px);
      display: flex;
      justify-content: space-between;

      .team__img {
        width: calc(50% - 20px);

        .team__imginner {
          padding-top: 76.79%;
        }
      }

      .team__info {
        width: calc(50% - 20px);
        padding: 60px;
        margin: 0;
        background-color: #F0F0F0;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .team__name {
          font-size: 26px;
        }

        .team__text {
          margin-top: 8px;
          font-size: 16px;
        }

        .team__add {
          margin-top: 28px;
          padding-top: 28px;
          font-size: 18px;
          position: relative;
          display: block;

          &::before {
            width: 50px;
            height: 3px;
            background-color: #000;
            content: '';
            position: absolute;
            left: 0;
            top: 0;
          }
        }

        .team__soc {
          margin-top: 25px;
        }
      }
    }
  }
}

.team__img {
  position: relative;
  overflow: hidden;

  .team__imginner {
    padding-top: 123%;
  }
}

.team__info {
  margin-top: 30px;
}

.team__name {
  font-size: 22px;
  line-height: 1.36;
  font-weight: 600;
}

.team__text {
  margin-top: 10px;
  font-size: 14px;
  color: #7a7a7a;

  a {
    color: $red;
  }
}

.team__soc {
  margin-top: 28px;

  li {
    margin-right: 8px;

    &:last-child {
      margin: 0;
    }

    a {
      display: block;

      img {
        width: 23px;
        height: 23px;
        display: block;
      }
    }
  }
}

.team__add {
  display: none;
}

.team__bottom {
  margin-top: 40px;
  justify-content: center;
}

.team__button {
  max-width: 270px;
  width: 100%;
}


@media screen and (max-width: 1240px) {
  .team__item {
    &:first-child {
      &:last-child {
        .team__img {
          width: calc(50% - 10px);
        }
        .team__info {
          width: calc(50% - 10px);
          .team__name {
            font-size: 24px;
          }
          .team__text {
            font-size: 14px;
          }
          .team__add {
            font-size: 16px;
          }
        }
      }
    }
  }
  .team__info {
    margin-top: 20px;
  }
  .team__name {
    font-size: 20px;
  }
  .team__text {
    font-size: 12px;
  }
  .team__soc {
    margin-top: 20px;
  }
}


@media screen and (max-width: 980px) {
  .section__team {
    padding: 40px 0;
  }
  .team__undertitle {
    font-size: 16px;
  }
  .team__list {
    margin: 0 -10px;
    margin-top: 30px;
    margin-bottom: -30px;
  }
  .team__item {
    width: calc(100% / 3 - 20px);
    margin: 0 10px;
    margin-bottom: 30px;
    &:first-child {
      &:last-child {
        width: calc(100% - 20px);
        .team__info {
          padding: 20px;
        }
      }
    }
  }
  .team__bottom {
    margin-top: 30px;
  }
}


@media screen and (max-width: 767px) {
  .team__item {
    width: calc(50% - 20px);
    &:first-child {
      &:last-child {
        width: calc(100% - 20px);
        display: block;
        .team__img {
          width: 100%;
          .team__imginner {
            padding-top: 71.17%;
          }
        }
        .team__info {
          margin-top: 30px;
          width: 100%;
          padding: 30px 20px;
        }
      }
    }
  }
  .team__soc {
    li {
      a {
        img {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}


@media screen and (max-width: 480px) {
  .section__team {
    .video__title {
      font-size: 28px;
      line-height: 1.3;
    }
  }
  .team__undertitle {
    font-size: 14px;
  }
  .team__list {
    margin-left: 0;
    margin-right: 0;
  }
  .team__item {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    &:first-child {
      &:last-child {
        width: 100%;
        .team__info {
          margin-top: 20px;
          padding: 20px 15px;
          .team__name {
            font-size: 22px;
          }
          .team__text {
            font-size: 12px;
          }
          .team__add {
            font-size: 14px;
          }
        }
      }
    }
  }
}