.section__evfirst {
  position: relative;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 2;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.event__dec {
  width: 910px;
  top: 59%;
  left: 55%;
  transform: translateX(-50%);
  position: absolute;
  z-index: 3;

  img {
    width: 100%;
    height: auto;
  }
}

.evfirst__container {
  padding-top: 190px;
  padding-bottom: 150px;
  min-height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  z-index: 10;
}

.evfirst__content {
  max-width: 940px;
  width: 100%;
  text-align: center;
}

.evfirst__topgreen {
  font-size: 21px;
  text-transform: uppercase;
  color: #42ae4d;
}

.evfirst__title {
  font-size: 45px;
  margin-top: 16px;
  line-height: 1.31;
  font-weight: 700;
  color: #fff;
}

.evfirst__text {
  font-size: 21px;
  margin-top: 22px;
  color: #fff;
  opacity: 0.75;
}

.evfirst__date {
  font-size: 29px;
  margin-top: 20px;
  font-weight: 700;
  color: #44e356;
}

.evfirst__button {
  margin-top: 28px;
  min-width: 245px;
}


@media screen and (max-width: 1600px) {
  .evfirst__container {
    padding-top: 150px;
  }
}


@media screen and (max-width: 1400px) {
  .evfirst__container {
    padding-top: 140px;
  }
}


@media screen and (max-width: 1240px) {
  .event__dec {
    width: 910px;
    top: 56%;
    left: 49.5%;
  }
  .evfirst__container {
    padding-top: 140px;
    padding-bottom: 100px;
  }
  .evfirst__content {
    max-width: 740px;
  }
  .evfirst__topgreen {
    font-size: 18px;
  }
  .evfirst__title {
    font-size: 34px;
    margin-top: 12px;
  }
  .evfirst__text {
    font-size: 18px;
    margin-top: 16px;
  }
  .evfirst__date {
    font-size: 18px;
    margin-top: 18px;
  }
  .evfirst__button {
    margin-top: 24px;
  }
}


@media screen and (max-width: 1140px) {
  .evfirst__container {
    padding-top: 100px;
  }
}

@media screen and (max-width: 980px) {
  .evfirst__container {
    padding-top: 100px;
  }
}


@media screen and (max-width: 767px) {
  .event__dec {
    width: 560px;
    top: 58%;
    left: 40%;
  }
  .evfirst__button {
    height: 48px !important;
  }
}


@media screen and (max-width: 480px) {
  .section__evfirst {
    background-color: #011438;
    &::after {
      display: none;
    }
  }
  .event__dec {
    width: 330px;
    top: 58%;
    left: 46%;
    opacity: .5;
  }
  .evfirst__topgreen {
    font-size: 14px;
  }
  .evfirst__title {
    font-size: 28px;
    margin-top: 18px;
  }
  .evfirst__text {
    font-size: 16px;
    margin-top: 12px;
  }
  .evfirst__date {
    margin-top: 12px;
  }
  .evfirst__button {
    margin-top: 18px;
    min-width: 225px;
  }
  .evfirst__button {
    height: 44px !important;
  }
  .event__bg {
    display: none;
  }
}