.section__events {
  padding: 60px 0;
}

.events__title {
  max-width: 800px;
  font-size: 27px;
  padding-bottom: 20px;
  width: 100%;
  line-height: 1.44;
  font-weight: 500;
  margin: 0 auto;
  position: relative;
  text-align: center;

  &::after {
    width: 54px;
    height: 3px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    background-color: $darkGreen;
    position: absolute;
    content: '';
  }
}

.events__list {
  margin: 0 -20px;
  margin-bottom: -60px;
  margin-top: 50px;
}

.events__item {
  width: calc(100% / 3 - 40px);
  margin: 0 20px;
  margin-bottom: 60px;
}

.event__img {
  padding-top: 83.33%;
  position: relative;
  overflow: hidden;
  display: block;
}

.events__info {
  margin-top: 20px;
}

.events__name {
  font-size: 20px;
  line-height: 1.35;
  color: $text;
  font-weight: 500;
}

.events__desc {
  margin-top: 12px;
  font-weight: 300;

  p {
    margin-bottom: 1em;

    &:last-child {
      margin: 0;
    }

    b, strong {
      font-weight: 700;
    }
  }
}

.events__pricecont {
  margin-top: 12px;
  align-items: center;
  align-content: center;
}

.event__price {
  font-size: 16px;
  line-height: 1.375;
  color: #000;

  del {
    padding-left: 12px;
    display: block;
    color: #bdbdbd;
  }
}

.events__btncont {
  margin-top: 20px;

  .button {
    max-width: 202px;
    width: 100%;
  }
}

.events__bottom {
  margin-top: 50px;
  justify-content: center;

  .button {
    max-width: 360px;
    width: 100%;
  }
}


.events__page {
  padding-top: 190px;

  .events__list, .latest__list {
    &:first-child {
      margin-top: 0 !important;
    }
  }

  .video__title {
    font-size: 27px;
    padding-bottom: 16px !important;
  }
}

@media screen and (max-width: 1600px) {
  .events__page {
    padding-top: 150px;
  }
}

@media screen and (max-width: 1400px) {
  .events__page {
    padding-top: 140px;
  }
}


@media screen and (max-width: 1240px) {
  .events__list {
    margin: 0 -10px;
    margin-bottom: -40px;
    margin-top: 40px;
  }
  .events__item {
    width: calc(100% / 3 - 20px);
    margin: 0 10px;
    margin-bottom: 40px;
  }
  .events__page {
    padding-top: 140px;
  }
}


@media screen and (max-width: 1140px) {
  .events__page {
    padding-top: 90px;
  }
}


@media screen and (max-width: 980px) {
  .section__events {
    padding: 40px 0;
  }
  .events__title {
    max-width: 690px;
    font-size: 20px;
  }
  .events__list {
    justify-content: center;
  }
  .events__item {
    width: calc(50% - 20px);
  }
  .events__name {
    font-size: 18px;
  }
  .events__desc {
    font-size: 12px;
  }
  .events__pricecont {
    margin-top: 10px;
  }

  .event__price {
    font-size: 14px;
    del {
      padding-left: 10px;
    }
  }
  .events__bottom {
    margin-top: 40px;
  }
  .events__page {
    padding-top: 90px;
    .video__title {
      font-size: 24px;
    }
  }
}


@media screen and (max-width: 767px) {
  .events__page {
    .video__title {
      font-size: 20px;
    }
  }
}


@media screen and (max-width: 480px) {
  .events__list {
    margin: 0;
    margin-top: 40px;
  }
  .events__item {
    width: 100%;
    margin: 0;
    margin-bottom: 40px;
    &:last-child {
      margin: 0;
    }
  }
  .events__page {
    .video__title {
      font-size: 19px;
    }
  }
}


@media screen and (max-width: 360px) {
  .events__title {
    font-size: 18px;
  }
  .events__name {
    font-size: 16px;
  }
  .events__btncont {
    .button {
      max-width: 100%;
    }
  }
}