html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}
body {
  overflow-x: hidden;
  position: relative;
}

* {
  box-sizing: border-box; }

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0; }

h1, h2, h3 {
  margin: 0;
  padding: 0;
  font-weight: normal; }

li {
  margin: 0;
  padding: 0;
  list-style: none; }

p {
  margin: 0;
}

input {
  outline: none;
  border: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

textarea {
  outline: none;
  border: none;
}

select {
  outline: none;
  border: none;
}

.wrapper {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.flexCentr {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.cover {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  height: 100% !important;
  object-fit: cover;
}

.contain {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.hr {
  width: 100%;
}



//-------------------------//

$text: #212121;
$blue: #001c45;
$white: #b8b8b8;
$darkBlue: #000712;
$turquoise: #15A69C;
$green: #64D471;
$darkGreen: #64D471;
$red: #ff8562;


body {
  font-size: 14px;
  color: #000;
  line-height: 1.5714;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;

  &.overHide {
    overflow: hidden;
  }

  &::after {
    content: '';
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-image: url(../img/backgrounds/bg.jpg);
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center top;
    opacity: .5;
    z-index: -1;
  }
}

.container {
  max-width: 1240px;
  padding: 0 40px;
  width: 100%;

  &.full {
    max-width: 100%;
  }
}

.progress {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 3px;
  background-color: $green;
  z-index: 110;
  width: 0;
}

.button {
  border-radius: 30px;
  padding: 0 20px;
  height: 45px;
  font-size: 14px;
  font-weight: 600;
  border: 2px solid $turquoise;
  color: #fff;
  display: inline-flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  transition: .3s;

  &.border {
    background-color: transparent;

    &:hover {
      background-color: $turquoise;
    }

    &.whiteB {
      border-color: #fff;

      &:hover {
        border-color: $turquoise;
      }
    }
  }

  &.full {
    background-color: $turquoise;

    &:hover {
      background-color: transparent;
      color: $text;
    }

    &.green {
      background-color: #42AE4D;
      border-color: #42AE4D;

      &:hover {
        background-color: $blue;
        border-color: $blue;
        color: #fff;
      }
    }
  }

  &.big {
    height: 58px;
    font-size: 16px;
    font-weight: 500;
  }
  
  &.black {
    color: $text;
    
    &:hover {
      color: #fff;
    }

    &.green {
      border-color: #43AE4E;

      &:hover {
        background-color: #43AE4E;
        border-color: #43AE4E;
      }
    }
  }
}


.text__wrapp {
  font-size: 20px;
  font-weight: 300;

  h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
    font-size: 1em;
  }

  b, strong {
    font-weight: 700;
  }

  & > * {
    margin-top: 1em;

    &:first-child {
      margin-top: 0;
    }
  }

  ul {
    margin-left: 1em;

    li {
      list-style: disc;
      margin-bottom: .35em;

      &:last-child {
        margin: 0;
      }
    }
  }

  ol {
    margin-left: 1em;

    li {
      list-style: decimal;
      margin-bottom: .35em;

      &:last-child {
        margin: 0;
      }
    }
  }

  a {
    color: $red;
    transition: .3s;

    &:hover {
      color: #000;
    }
  }

  blockquote {
    padding-left: 35px;
    border-left: 2px solid #000;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 0;
  }

  p {
    min-height: .1em;
  }

  table {
    border: 1px solid rgba(0,0,0,.1);
    max-width: 100%;
    display: block;
    overflow: auto;
    td {
      border: 1px solid rgba(0,0,0,.1);
      padding: 0.5em;
    }
  }

  figure {
    margin: 0;
    padding: 0;

    img {
      display: block;
      max-width: calc(50vw - 15px);
      height: auto;
    }
  }

  .wp-block-media-text {
    display: flex;

    figure {
      margin-right: 15px;
      max-width: 50%;

      &:last-child {
        margin-left: 15px;
        margin-right: 0;
      }
    }

    &.is-vertically-aligned-top {
      align-content: flex-start;
      align-items: flex-start;
    }

    &.is-vertically-aligned-center {
      align-items: center;
      align-content: center;
    }

    &.is-vertically-aligned-center {
      align-items: flex-end;
      align-content: flex-end;
    }
  }

  .wp-block-media-text__content {
    & > * {
      margin-top: 1em;

      &:first-child {
        margin-top: 0;
      }
    }
  }

  pre {
    background-color: rgba(0,0,0,.05);
    padding: 1em;
  }

  .alignright {
    float: right;
    margin-left: 2em;
    max-width: calc(50vw - 15px);
    margin-top: 1em;
    height: auto;
  }

  .alignleft {
    float: right;
    margin-right: 2em;
    max-width: calc(50vw - 15px);
    margin-top: 1em;
    height: auto;
  }
}


.nav-links, .page-numbers, .facetwp-pager {
  align-items: center;
  align-content: center;
  display: flex;
  flex-wrap: wrap;

  .pages {
    display: flex;
    flex-wrap: wrap;
  }
}

.paging-navigation, .facetwp-pager {
  text-align: center;

  a, span, .facetwp-page {
    color: $text;
    padding: 6px 14px;
    font-size: 1em;
    text-align: center;
    margin: 0 1px;
    display: inline-block;
    white-space: nowrap;
    border-radius: 50%;
  }

  a {
    color: $text !important;
  }

  .current, .active {
    background-color: $darkGreen;
    color: #fff !important;
    font-weight: 400 !important;
  }

  span {
    color: $text;
  }
}

a.page-numbers, .facetwp-page {
  color: $text !important;

  &.next {
    width: 34px;
    height: 34px;
    -webkit-background-size: 14px;
    background-size: 14px;
    padding: 0;
    background-repeat: no-repeat;
    background-position: center center;
    display: block;
    font-size: 0;
    background-image: url("data:image/svg+xml,%3Csvg height='800px' width='800px' version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' viewBox='0 0 49.656 49.656' xml:space='preserve'%3E%3Cg%3E%3Cpolygon style='fill:%23000;' points='14.535,48.242 11.707,45.414 32.292,24.828 11.707,4.242 14.535,1.414 37.949,24.828 '/%3E%3Cpath style='fill:%23000;' d='M14.535,49.656l-4.242-4.242l20.585-20.586L10.293,4.242L14.535,0l24.829,24.828L14.535,49.656z M13.121,45.414l1.414,1.414l22-22l-22-22l-1.414,1.414l20.585,20.586L13.121,45.414z'/%3E%3C/g%3E%3C/svg%3E");
  }

  &.prev {
    width: 34px;
    height: 34px;
    -webkit-background-size: 14px;
    background-size: 14px;
    padding: 0;
    background-repeat: no-repeat;
    background-position: center center;
    display: block;
    font-size: 0;
    background-image: url("data:image/svg+xml,%3Csvg height='800px' width='800px' version='1.1' id='Capa_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' viewBox='0 0 49.656 49.656' xml:space='preserve'%3E%3Cg%3E%3Cpolygon style='fill:%23000;' points='35.121,1.414 37.949,4.242 17.364,24.828 37.949,45.414 35.121,48.242 11.707,24.828 '/%3E%3Cpath style='fill:%23000;' d='M35.122,49.656L10.292,24.828L35.121,0l4.242,4.242L18.778,24.828l20.586,20.586L35.122,49.656z M13.121,24.828l22.001,22l1.414-1.414L15.95,24.828L36.535,4.242l-1.414-1.414L13.121,24.828z'/%3E%3C/g%3E%3C/svg%3E");
  }
}

.text__content {
  max-width: 780px;
  margin: 0 auto;
}

.page__title {
  font-size: 20px;
  text-align: center;
  font-weight: 700;
}

.page__content {
  margin-top: 32px;
}

.noresults {
  font-size: 1.2em;
  font-weight: 600;
  width: 100%;
  text-align: center;
}


@media screen and (max-width: 1240px) {
  .container {
    padding: 0 15px;
  }
  .text__wrapp {
    font-size: 18px;
    h1 {
      font-size: 2em;
    }
    figure {
      margin: 0;
      padding: 0;

      img {
        max-width: 100%;
      }
    }
    .wp-block-media-text {
      display: block;
      figure {
        margin: 0;
        margin-bottom: 1em;
        max-width: 100%;
        width: 100%;
        &:last-child {
          margin: 0;
          margin-top: 1em;
        }
      }
    }
    .alignright {
      float: none;
      margin: 0 auto;
      max-width: 100%;
      margin-top: 1em;
      height: auto;
      display: block;
    }

    .alignleft {
      display: block;
      margin: 0 auto;
      float: none;
      max-width: 100%;
      margin-top: 1em;
      height: auto;
    }
  }
}


@media screen and (max-width: 980px) {
  .progress {
    z-index: 90;
  }
  .page__title {
    font-size: 18px;
  }
  .page__content {
    margin-top: 24px;
    font-size: 18px;
  }
}


@media screen and (max-width: 767px) {
  .button {
    &.big {
      height: 52px;
    }
  }
  .text__wrapp {
    font-size: 16px;
  }
  .page__title {
    font-size: 16px;
  }
  .page__content {
    font-size: 16px;
  }
}