.xoo-wsc-basket  {
  right: 5px !important;
  top: 115px !important;
  border-radius: 50% !important;
  z-index: 120 !important;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, .2) !important;
  background-color: rgba(255, 255, 255, .8) !important;
  padding: 0 !important;
  width: 58px !important;
  height: 58px !important;
  border: none !important;
  .xoo-wsc-items-count {
    bottom: -10px;
    left: -10px;
  }
  .xoo-wsc-bki {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}

.xoo-wsc-container {
  overflow: auto;
  position: fixed;
  max-width: calc(100% - 30px);
  width: 560px;
  left: 50% !important;
  top: 50% !important;
  bottom: inherit !important;
  max-height: calc(100vh - 30px) !important;
  transform: translateY(-50%) translateX(-50%);
  opacity: 0;
  visibility: hidden;
  transition: .4s opacity;
}

.xoo-wsc-modal.xoo-wsc-cart-active {
  .xoo-wsc-container {
    opacity: 1 !important;
    visibility: visible !important;
  }
}

.xoo-wsch-text {
  margin: 0 !important;
  padding: 0 !important;
}

.xoo-wsc-sum-col {
  padding: 0 !important;
}

.xoo-wsc-qty-price {
  display: none !important;
  justify-content: space-between;
}

.xoo-wsc-sm-info {
  display: flex;
  align-items: center;
  align-content: center;
}

.xoo-wsc-sm-right {
  padding-left: 20px !important;
  flex-direction: row;
  max-width: inherit;
  flex-wrap: nowrap;
  align-items: center;
  align-content: center;

  .xoo-wsc-smr-del {
    order: 2;
    margin: 0;
    margin-left: 15px;
    width: 20px;
    height: 20px;
    opacity: .4;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
    background-image: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='64px' height='64px' viewBox='0 0 64 64' enable-background='new 0 0 64 64' xml:space='preserve'%3E%3Cg%3E%3Cline fill='none' stroke='%23000000' stroke-width='2' stroke-miterlimit='10' x1='18.947' y1='17.153' x2='45.045' y2='43.056'/%3E%3C/g%3E%3Cg%3E%3Cline fill='none' stroke='%23000000' stroke-width='2' stroke-miterlimit='10' x1='19.045' y1='43.153' x2='44.947' y2='17.056'/%3E%3C/g%3E%3Cg%3E%3Ccircle fill='none' stroke='%23000000' stroke-width='2' stroke-miterlimit='10' cx='32' cy='32' r='30.999'/%3E%3C/g%3E%3C/svg%3E%0A");

    &::before {
      display: none;
    }
  }

  .xoo-wsc-smr-ptotal {
    min-width: 70px;
  }
}

.xoo-wsc-ft-buttons-cont {
  .button {
    background-color: $turquoise;
    color: #fff;
    
    &:hover {
      background-color: #fff;
      color: #000;
    }
  }
}

.couponForm  {
  justify-content: space-between;
  width: 100%;

  input {
    height: 36px;
    width: calc(60% - 4px);
    border-radius: 30px;
    border: 1px solid $turquoise;
    padding: 0 12px;
    font-size: 14px;
  }

  a {
    height: 36px !important;
    font-size: 14px !important;
    width: calc(40% - 4px);
  }
}

.xoo-wsc-body {
  border-bottom: 1px solid $turquoise;
}

.xoo-wsc-product {
  &:last-child {
    border: none;
  }
}

.xoo-wsc-header {
  padding: 11px 14px !important;
}

.xoo-wsc-empty-cart {
  font-size: 18px;
  font-weight: 600;
}


@media screen and (max-width: 980px) {
  .xoo-wsc-basket  {
    top: 75px !important;
  }
}


@media screen and (max-width: 480px) {
  .xoo-wsc-body, .xoo-wsc-ft-totals, .xoo-wsc-ft-buttons-cont .button {
    font-size: 14px !important;
    * {
      font-size: 14px !important;
    }
  }
  .xoo-wsc-sm-info {
    padding-bottom: 20px;
    position: relative;
    .xoo-wsc-smr-ptotal {
      position: absolute;
      left: 0;
      bottom: 0;
    }
    .xoo-wsc-smr-del {
      margin: 0 !important;
    }
  }
  .xoo-wsc-sm-right {
    padding-left: 10px !important;
  }
  .couponForm  {
    input {
      height: 32px;
      width: calc(60% - 2px);
      font-size: 12px;
    }

    a {
      height: 32px !important;
      font-size: 12px !important;
      width: calc(40% - 2px);
    }
  }
}