.section__video {
  padding: 60px 0;
}

.video__title {
  font-size: 42px;
  padding-bottom: 5px;
  font-weight: 600;
  color: $blue;
  text-align: center;
  position: relative;

  &::after {
    width: 50px;
    height: 3px;
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: $green;
  }
}

.video__container {
  margin: 0 auto;
  margin-top: 35px;
  max-width: 960px;
  width: 100%;
}

.video__inner {
  position: relative;
  width: 100%;
  padding-top: calc(100% / 16 * 9);
  background-color: #000;

  iframe {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.video__infogr {
  margin: 0 -10px;
  margin-top: 56px;
  justify-content: space-between;
  flex-wrap: nowrap;
}

.infogr__item {
  margin: 0 10px;
  min-width: 90px;
}

.infogr__img {
  width: 70px;
  height: 80px;
  position: relative;
  margin: 0 auto;

  img {
    position: relative;
    z-index: 2;
  }

  &::before {
    width: 62px;
    height: 61px;
    bottom: -4px;
    left: 0;
    transform: translateX(-40%);
    border-radius: 50%;
    background-color: #5ad6d0;
    opacity: .1;
    content: '';
    position: absolute;
  }
}

.infogr__text {
  margin-top: 12px;
  font-size: 18px;
  text-align: center;
  color: #000;
}


@media screen and (max-width: 980px) {
  .section__video {
    padding: 40px 0;
  }
  .video__title {
    font-size: 32px;
    padding-bottom: 8px;
  }
  .video__container {
    margin-top: 24px;
  }
  .video__infogr {
    margin-top: 40px;
  }
  .infogr__img {
    width: 60px;
    height: 70px;
    &::before {
      width: 52px;
      height: 51px;
      bottom: -2px;
      transform: translateX(-45%);
    }
  }
  .infogr__text {
    margin-top: 10px;
    font-size: 16px;
  }

}

@media screen and (max-width: 767px) {
  .video__title {
    font-size: 30px;
    line-height: 1.3;
  }
  .video__container {
    width: auto;
    margin-left: -15px;
    margin-right: -15px;
  }
  .video__infogr {
    margin-bottom: -30px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .infogr__item {
    margin-bottom: 30px;
    width: calc(100% / 3 - 20px);
  }
}


@media screen and (max-width: 480px) {
  .infogr__item {
    width: calc(50% - 20px);
  }
}