.passwerd__reseted {
  position: fixed;
  z-index: 101;
  left: 50%;
  transform: translateX(-50%);
  top: 47px;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  align-items: center;
  align-content: center;
  justify-content: center;
  display: flex;
  width: 234px;
  height: 48px;
  background-color: #43AE4E;
  border-radius: 5px;

  &::before {
    content: '';
