  right: 0;
  bottom: 0;
  top: 0;
}

.som-password-error-message {
  display: none;
}


@media screen and (max-width: 980px) {
  .lrm-user-modal-container {
    padding: 45px 20px !important;
    .lrm-switcher {
      li {
        a {
          font-size: 17px;
        }
      }
    }
    .lrm-signin-section, .lrm-signup-section, .lrm-reset-password-section {
      margin-top: 40px;
      .lrm-form {
        .fieldset {
          margin-bottom: 20px !important;
          .lrm-col-half-width {
            margin-bottom: 20px !important;
          }
          input {
            border-radius: 3px !important;
            padding: 0 18px !important;
            font-size: 12px !important;
            height: 45px !important;
            &[name="password"] {
              padding-right: 48px !important;
            }
          }
          .hide-password {
            width: 48px !important;
            height: 42px !important;
            -webkit-background-size: 18px !important;
            background-size: 18px !important;
          }
          .lrm-nice-checkbox__label {
            padding-left: 28px !important;
            font-size: 12px !important;
            &.lrm-accept-terms-checkbox {
              font-size: 12px !important;
            }
          }
          .lrm-error-message {
            font-size: 10px !important;
            line-height: 14px !important;
          }
        }
        .fieldset--submit {
          button {
            height: 45px !important;
            border-radius: 45px !important;
            font-size: 13px !important;
          }
        }
      }
      .lrm-form-bottom-message {
        font-size: 12px !important;
      }
    }
    .lrm-reset-password-section {
      .lrm-form {
        .fieldset {
          margin-bottom: 5px !important;
          &.fieldset--terms {
            margin-bottom: 18px !important;
          }
        }
      }
      .lrm-form-message {
        margin-bottom: 18px !important;
      }
    }
    .lrm-form-message {
      margin-bottom: 20px !important;
      font-size: 12px !important;
    }
  }
  //////////////
  .reset__popcont {
    padding: 45px 20px;
    fieldset {
      legend {
        font-size: 17px;
        margin-bottom: 46px;
      }
      .somfrp-lost-pass-form-text {
        margin-top: 7px;
        font-size: 10px;
        line-height: 14px;
        & + div {
          p {
            margin-bottom: 20px;
            .resErText {
              font-size: 10px;
            }
            input {
              border-radius: 3px;
              padding: 0 18px;
              font-size: 12px;
              height: 45px;
              padding-right: 48px;
            }
            .hide-password {
              width: 48px;
              height: 42px;
              -webkit-background-size: 18px;
              background-size: 18px;
            }
          }
        }
      }
      .lostpassword-submit {
        margin-top: 30px;
        button, .button {
          height: 45px !important;
          border-radius: 45px;
          font-size: 13px !important;
        }
      }
    }
    .gotpass {
      font-size: 12px;
    }
  }
  .som-password-error-message {
    font-size: 12px !important;
    margin-bottom: 20px !important;
  }
}


@media screen and (max-width: 480px) {
  .lrm-user-modal-container, .reset__popcont {
    padding-top: 100px !important;
    transform: none !important;
    left: 0 !important;
    bottom: 0 !important;
    top: 0 !important;
    right: 0 !important;
    max-width: 100% !important;
    max-height: inherit !important;
  }
  .liginTop {
    display: flex;
    align-items: center;
    align-content: center;
    padding: 0 20px;
    height: 55px;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 200;
    border-bottom: 1px solid #ECECEC;
    background-color: #fff;
    .loginTopT {
      font-size: 14px;
    }
    .lrm-close-formn {
      width: 20px;
      height: 20px;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-position: center center;
      -webkit-background-size: 14px;
      background-size: 14px;
      font-size: 0;
      position: absolute;
      background-image: url("data:image/svg+xml,%0A%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.8925 1.10742L1.10742 12.8925M12.8925 12.8925L1.10742 1.10742' stroke='%23808080' stroke-width='1.7' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    }
  }
}

@media screen and (max-width: 375px) {
  .lrm-user-modal-container, .reset__popcont {
    padding-left: 15px !important;
    padding-right: 15px !important;
  }
  .liginTop {
    padding: 0 15px;
    .lrm-close-formn {
      right: 15px;
    }
  }
}