.section__error {
  position: relative;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: -webkit-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -o-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: -ms-linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    background: linear-gradient(180deg, rgba(0,28,69,0.90) 0%, rgba(0,26,97,0.70) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorStr='#19001c45', endColorstr='#4c001a61');
  }
}

.error__content {
  padding: 90px 0;
  min-height: 100vh;
  position: relative;
  z-index: 2;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-direction: column;
}

.error__title {
  font-size: 45px;
  line-height: 1.31;
  text-align: center;
  font-weight: 700;
  color: #fff;
}

.error__button {
  max-width: 246px;
  width: 100%;
  margin-top: 16px;
}


@media screen and (max-width: 980px) {
  .error__title {
    font-size: 28px;
    line-height: 1.25;
  }
}


@media screen and (max-width: 767px) {
  .error__title {
    font-size: 26px;
  }
}