.lebrary__container {
  justify-content: space-between;
}

.library__aside {
  width: 33.53%;
  border: 1px solid #ECECEC;
  background-color: #fff;
}

.library__filtercont {
  padding: 35px;
  padding-top: 30px;
}

.filter__title {
  margin-bottom: 16px;
  font-weight: 600;
}

.filter__part {
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ECECEC;

  &:last-child {
    margin-bottom: 0;
    padding-bottom: 0;
    border: none;
  }
}

.facetwp-facet {
  margin-bottom: 0 !important;
}

.search__form {
  position: relative;

  .facetwp-input-wrap {
    width: 100%;
    display: block;
  }

  .search__input, input {
    border: 1px solid #D2D2D2;
    border-radius: 5px;
    padding-left: 17px;
    padding-right: 44px;
    height: 45px;
    display: block;
    width: 100%;
  }
  
  .search__btn, .facetwp-icon {
    width: 18px;
    height: 18px;
    right: 16px;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: transparent;
    cursor: pointer;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.5605 15.4395L13.7528 11.6318C14.5395 10.446 15 9.02625 15 7.5C15 3.3645 11.6355 0 7.5 0C3.3645 0 0 3.3645 0 7.5C0 11.6355 3.3645 15 7.5 15C9.02625 15 10.446 14.5395 11.6318 13.7528L15.4395 17.5605C16.0245 18.1462 16.9755 18.1462 17.5605 17.5605C18.1462 16.9748 18.1462 16.0252 17.5605 15.4395ZM2.25 7.5C2.25 4.605 4.605 2.25 7.5 2.25C10.395 2.25 12.75 4.605 12.75 7.5C12.75 10.395 10.395 12.75 7.5 12.75C4.605 12.75 2.25 10.395 2.25 7.5Z' fill='%2315A79D'/%3E%3C/svg%3E%0A");

    &::before {
      display: none;
    }
  }
}

.filter__form {
  margin-bottom: -12px;

  &.labels {
    margin-bottom: -15px;

    &.flex {
      margin-right: -15px;

      .filter__item, .facetwp-checkbox {
        margin-right: 15px;
      }
    }

    .filter__item, .facetwp-checkbox {
      margin-bottom: 15px;
      display: inline-flex;
      height: 30px;
      padding: 0 17px;
      background-color: #ECECEC;
      border-radius: 30px;
      align-items: center;
      align-content: center;
      justify-content: center;
      color: #000;
      transition: .3s;
      cursor: pointer;

      .facetwp-display-value {
        padding: 0;
        &::before {
          display: none;
        }
      }

      .facetwp-counter {
        display: none;
      }

      &.checked {
        background-color: #43AE4E;
        color: #fff;
      }
    }
  }

  .filter__item, .facetwp-checkbox {
    margin-bottom: 12px;
    background-image: none !important;
    padding: 0;
    display: flex;

    &.checked {
      .facetwp-display-value {
        &::before {
          background-color: #43AE4E;
          background-image: url("data:image/svg+xml,%0A%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.375 2.5C9.375 2.65992 9.31396 2.81992 9.19189 2.9418L4.19189 7.9418C4.07031 8.06445 3.91016 8.125 3.75 8.125C3.58984 8.125 3.43008 8.06396 3.3082 7.94189L0.808203 5.44189C0.686133 5.32031 0.625 5.16016 0.625 5C0.625 4.64297 0.916992 4.375 1.25 4.375C1.40992 4.375 1.56992 4.43604 1.6918 4.55811L3.75 6.61719L8.30859 2.05859C8.42969 1.93613 8.58984 1.875 8.75 1.875C9.08398 1.875 9.375 2.14258 9.375 2.5Z' fill='white'/%3E%3C/svg%3E%0A");
        }
      }
    }

    &.label {
      margin-bottom: 15px;
      display: inline-flex;

      input {
        display: none;

        &:checked {
          & + .filter__field {
            background-color: #43AE4E;
            color: #fff;
          }
        }
      }

      .filter__field {
        height: 30px;
        padding: 0 17px;
        background-color: #ECECEC;
        border-radius: 30px;
        display: inline-flex;
        align-items: center;
        align-content: center;
        justify-content: center;
        color: #000;
        transition: .3s;
        cursor: pointer;
      }
    }

    input {
      display: none;
      
      &:checked {
        & + .filter__field {
          .filter__check {
            background-color: #43AE4E;
            background-image: url("data:image/svg+xml,%0A%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.375 2.5C9.375 2.65992 9.31396 2.81992 9.19189 2.9418L4.19189 7.9418C4.07031 8.06445 3.91016 8.125 3.75 8.125C3.58984 8.125 3.43008 8.06396 3.3082 7.94189L0.808203 5.44189C0.686133 5.32031 0.625 5.16016 0.625 5C0.625 4.64297 0.916992 4.375 1.25 4.375C1.40992 4.375 1.56992 4.43604 1.6918 4.55811L3.75 6.61719L8.30859 2.05859C8.42969 1.93613 8.58984 1.875 8.75 1.875C9.08398 1.875 9.375 2.14258 9.375 2.5Z' fill='white'/%3E%3C/svg%3E%0A");
          }
        }
      }
    }

    .filter__field, .facetwp-display-value {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      align-content: center;

      .filter__check, &::before {
        width: 16px;
        min-width: 16px;
        height: 16px;
        margin-right: 10px;
        border: 1px solid #D2D2D2;
        border-radius: 2px;
        position: relative;
        transition: .3s;
        background-repeat: no-repeat;
        background-position: center center;
        -webkit-background-size: 10px;
        background-size: 10px;
        content: '';
      }
    }
  }

  &.flex {
    margin-right: -34px;

    .facetwp-facet {
      display: flex;
      flex-wrap: wrap;
    }

    .filter__item, .facetwp-checkbox {
      margin-right: 34px;
    }
  }
}

///////////
.library__main {
  width: 63.28%;
}

.library__title {
  font-size: 21px;
  font-weight: 600;
}

.library__list {
  margin-top: 32px;
  margin-bottom: -20px;
  justify-content: space-between;

  .noresults {
    text-align: left;
  }
}

.library__item {
  width: calc(50% - 10px);
  padding: 30px;
  padding-right: 26px;
  margin-bottom: 20px;
  border: 1px solid #ECECEC;
  background-color: #fff;
}

.library__itemtop {
  padding-left: 10px;
}

.library__icon {
  width: 57px;
  min-width: 57px;
  height: 75px;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: contain;
  background-size: contain;
  position: relative;
  background-image: url("data:image/svg+xml,%0A%3Csvg width='57' height='75' viewBox='0 0 57 75' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M57 19.7367V69.0785C57 72.3492 54.3594 75 51.1034 75H5.89659C2.63945 75 0 72.3492 0 69.0785V5.92148C0 2.65078 2.63945 0 5.89659 0H37.3443L57 19.7367Z' fill='%23E2E5E7'/%3E%3Cpath d='M43 18.5L57.5 33V18.5H43Z' fill='%23CAD1D8'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M56.9998 18.6316V21.0879H40.9316C37.6047 21.0879 36.0703 18.3691 36.0703 15.0164V0H38.5039L56.9998 18.6316Z' fill='%23B0B7BD'/%3E%3C/svg%3E%0A");
}

.library__dockname {
  width: 57px;
  height: 29px;
  top: 35px;
  left: -10px;
  line-height: 29px;
  font-size: 16px;
  border-radius: 2px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  position: absolute;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.1);
  display: inline-block;

  &.yt {
    width: 43px;
    height: 30px;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
    position: relative;
    font-size: 0;
    background-color: transparent !important;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='43' height='31' viewBox='0 0 43 31' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M42.1016 5.1502C41.6072 3.30002 40.1502 1.84314 38.3001 1.34864C34.9464 0.450012 21.5 0.450012 21.5 0.450012C21.5 0.450012 8.05351 0.450012 4.69985 1.34864C2.85001 1.84314 1.3928 3.30002 0.898381 5.1502C0 8.50353 0 15.5003 0 15.5003C0 15.5003 0 22.4968 0.898381 25.8498C1.3928 27.7 2.85001 29.1573 4.69985 29.6517C8.05351 30.55 21.5 30.55 21.5 30.55C21.5 30.55 34.9464 30.55 38.3001 29.6517C40.1502 29.1573 41.6072 27.7 42.1016 25.8498C42.9999 22.4968 42.9999 15.5003 42.9999 15.5003C42.9999 15.5003 42.9999 8.50353 42.1016 5.1502ZM17.1997 21.9503V9.0501L28.3713 15.5003L17.1997 21.9503Z' fill='%23F70000'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
  }
  
  &.kdm {
    -webkit-background-size: 45px;
    background-size: 45px;
    font-size: 0;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='45' height='17' viewBox='0 0 45 17' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Crect width='45' height='17' fill='url(%23pattern0)'/%3E%3Cdefs%3E%3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E%3Cuse xlink:href='%23image0_96_42' transform='translate(-0.000775194) scale(0.00439276 0.0116279)'/%3E%3C/pattern%3E%3Cimage id='image0_96_42' width='228' height='86' xlink:href='data:image/png;base64,'/%3E%3C/defs%3E%3C/svg%3E%0A");
  }
}

.library__name {
  padding-top: 3px;
  padding-left: 24px;
  width: calc(100% - 57px);
  line-height: 1.71;
  color: #000;
}

.library__tags {
  margin-top: 24px;
  margin-bottom: -10px;
  margin-right: -10px;

  li {
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 0 11px;
    height: 25px;
    border-radius: 25px;
    background: #ECECEC;
    color: #808080;
    font-size: 12px;
    display: inline-flex;
    align-items: center;
    align-content: center;
  }
}

.library__pag {
  justify-content: flex-end;

  .facetwp-type-pager {
    margin-top: 50px !important;
  }
}


@media screen and (max-width: 1240px) {
  .lebrary__container {
    padding-top: 0;
    justify-content: space-between;
  }
  .library__filtercont {
    padding: 20px;
  }
  .filter__form {
    &.flex {
      margin-right: -24px;

      .filter__item {
        margin-right: 24px;
      }
    }
  }
  ///////////
  .library__list {
    margin-top: 24px;
  }
  .library__item {
    padding: 15px;
  }
  .library__itemtop {
    padding-left: 6px;
  }
  .library__name {
    padding-top: 0;
  }
}

@media screen and (max-width: 980px) {
  .library__title {
    font-size: 17px;
  }
  .library__list {
    margin-top: 25px;
  }
  .library__icon {
    width: 34px;
    min-width: 34px;
    height: 44px;
  }
  .library__dockname {
    width: 34px;
    height: 14px;
    top: 22px;
    left: -6px;
    line-height: 14px;
    font-size: 10px;
    &.yt {
      width: 25px;
      height: 17px;
    }
    &.kdm {
      -webkit-background-size: 26px;
      background-size: 26px;
    }
  }
  .library__name {
    padding-top: 0;
    padding-left: 14px;
    font-size: 12px;
    width: calc(100% - 34px);
  }
  .library__tags {
    margin-top: 10px;
    li {
      padding: 0 10px;
      height: 19px;
      border-radius: 19px;
      font-size: 9px;
    }
  }
  .library__pag {
    .facetwp-type-pager {
      margin-top: 40px !important;
    }
  }
}


@media screen and (min-width: 767px) {
  .library__aside {
    display: block !important;
  }
  .filter__mobtop, .openfilter__cont {
    display: none !important;
  }
}

@media screen and (max-width: 767px) {
  .library__aside {
    padding-top: 55px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 150;
    width: inherit;
    border: none;
    display: none;
    .openfilter__cont {
      position: absolute;
    }
  }
  .library__main {
    width: 100%;
  }
  .library__pag {
    justify-content: center;
  }
  .filter__mobtop {
    padding: 0 15px;
    display: flex;
    height: 55px;
    align-items: center;
    align-content: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    border-bottom: 1px solid #ECECEC;
  }
  .filter__close {
    width: 20px;
    height: 20px;
    right: 15px;
    display: block;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-image: url("data:image/svg+xml,%0A%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.8925 4.10744L4.10742 15.8926M15.8925 15.8926L4.10742 4.10744' stroke='%23808080' stroke-width='1.7' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  }
  .library__filtercont {
    height: calc(100% - 85px);
    padding: 15px;
    padding-top: 30px;
    overflow-y: auto;
    overflow-x: hidden;
  }
}


@media screen and (max-width: 480px) {
  .library__item {
    width: 100%;
  }
  .openfilter__cont {
    padding: 20px;
    display: flex;
    justify-content: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 100;
  }

  .filter__btn {
    max-width: 328px;
    height: 45px !important;
    font-size: 13px !important;
    width: 100%;
  }
}

