.section__latest {
  padding: 60px 0;
}

.news__title {
  font-size: 42px;
  padding-bottom: 8px;
  font-weight: 600;
  color: $blue;
  text-align: center;
  position: relative;

  &::after {
    width: 54px;
    height: 3px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
    background-color: $darkGreen;
    position: absolute;
    content: '';
  }
}

.latest__undertitle {
  margin-top: 16px;
  font-size: 17px;
  opacity: .6;
  text-align: center;
}

.latest__list {
  margin: 0 -20px;
  margin-top: 40px;
  margin-bottom: -60px;
}

.latest__article {
  width: calc(100% / 3 - 40px);
  margin: 0 20px;
  margin-bottom: 60px;
}

.latest__img {
  padding-top: 75%;
  display: block;
  overflow: hidden;
  position: relative;
}

.latest__info {
  margin-top: 20px;
}

.latest__title {
  font-size: 20px;
  line-height: 1.35;
  color: #000;
  font-weight: 600;
}

.latest__date {
  margin-top: 12px;
  font-size: 10px;
  color: #000;
  opacity: .6;
  font-weight: 600;
  display: block;
}


@media screen and (max-width: 1240px) {
  .latest__list {
    margin: 0 -10px;
    margin-bottom: -40px;
    margin-top: 40px;
  }
  .latest__article {
    width: calc(100% / 3 - 20px);
    margin: 0 10px;
    margin-bottom: 40px;
  }
}


@media screen and (max-width: 980px) {
  .section__latest {
    padding: 40px 0;
  }
  .news__title {
    font-size: 38px;
  }
  .latest__undertitle {
    font-size: 16px;
  }
  .latest__list {
    justify-content: center;
  }
  .latest__article {
    width: calc(50% - 20px);
  }
  .latest__title {
    font-size: 18px;
  }
}


@media screen and (max-width: 767px) {
  .news__title {
    font-size: 30px;
    padding-bottom: 8px;
  }
  .latest__undertitle {
    font-size: 14px;
    margin-top: 10px;
  }
  .latest__list {
    margin: 0;
    margin-top: 40px;
  }
  .latest__article {
    width: 100%;
    margin: 0;
    margin-bottom: 40px;
    &:last-child {
      margin: 0;
    }
  }
}


@media screen and (max-width: 360px) {
  .latest__title {
    font-size: 16px;
  }
}