.whiteTheme {
  .header__nav {
    li {
      a {
        color: $text;
      }

      &.current-menu-item {
        a {
          color: $text !important;
        }
      }
    }
  }
  .header__enter {
    background-color: #fff;
    &::before {
      filter: none;
    }
  }
  .headerenter__text {
    span {
      &:first-child {
        color: #808080;
      }
      &:last-child {
        color: #031C45;
      }
    }
  }
}

.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
}

.header__content {
  height: 190px;
  align-items: center;
  align-content: center;
  justify-content: space-between;
}

.header__part {
  align-items: center;
  align-content: center;
}

.custom-logo-link {
  width: 260px;
  display: block;

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.header__nav {
  li {
    margin-right: 30px;
    
    &:last-child {
      margin: 0;
    }
    
    a {
      font-size: 16px;
      text-transform: uppercase;
      color: #fff;
      transition: .3s;
      position: relative;
      display: block;

      &.pointerCur {
        &::after {
          background-color: $darkGreen;
        }
      }

      &::after {
        height: 2px;
        background-color: transparent;
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        transition: .3s;
      }

      &:hover {
        color: $darkGreen !important;
      }
    }

    &.current-menu-item {
      opacity: .7;

      a {
        color: #fff !important;

        &::after {
          background-color: $darkGreen;
        }
      }
    }
  }
}

