.headerenter__text {
  span {
    line-height: 1.2;
    
    &:first-child {
      font-size: 12px;
      color: #CDCDCD;
      display: block;
    }

    &:last-child {
      color: #fff;
      font-weight: 600;
    }
  }
}

.header__soc {
  margin-left: 40px;

  li {
    margin-right: 10px;

    &:last-child {
      margin: 0;

      a {
        display: block;

        img {
          width: 30px;
          height: 30px;
          display: block;
        }
      }
    }
  }
}

.nav__close {
  display: none;
}

.hamburger {
  display: none;
}

@media screen and (max-width: 1600px) {
  .header__content {
    height: 150px;
  }
  .custom-logo-link {
    width: 200px;
  }
  .header__nav {
    li {
      margin-right: 20px;
    }
  }
  .header__enter {
    width: 220px;
    height: 56px;
    border-radius: 56px;
    margin-left: 30px;
    padding: 0 10px;
    &::before {
      margin-right: 15px;
    }
  }
  .header__soc {
    margin-left: 30px;
  }
}

@media screen and (max-width: 1400px) {
  .custom-logo-link {
    width: 160px;
  }
  .header__nav {
    li {
      margin-right: 15px;
      a {
        font-size: 14px;
      }
    }
  }
  .header__enter {
    width: 180px;
    height: 44px;
    border-radius: 44px;
    margin-left: 20px;
    &::before {
      width: 20px;
      height: 20px;
      margin-right: 10px;
    }
  }
  .headerenter__text {
    span {
      &:first-child {
        font-size: 10px;
      }
      &:last-child {
        font-size: 12px;
      }
    }
  }
  .header__soc {
    margin-left: 20px;
  }
}

@media screen and (max-width: 1240px) {
  .header__content {
    height: 140px;
  }
}


@media screen and (min-width: 1140px) {
  .header {
    &.fixed {
      position: fixed;
      background-color: #fff;
      transform: translateY(-100%);
      opacity: 0;
      &.fixedF {
        transform: translateY(0);
        opacity: 1;
        transition: .3s;
      }
      .header__content {
        height: 110px;
      }
      .custom-logo-link {
        width: 160px;
        display: block;
      }
      .header__nav {
        li {
          a {
            color: $blue;
          }
          &.current-menu-item {
            a {
              color: $blue !important;
            }
          }
        }
      }
      .header__enter {
        background-color: #fff;
        &::before {
          filter: none;
        }
      }
      .headerenter__text {
        span {
          &:first-child {
            color: #808080;
          }
          &:last-child {
            color: #031C45;
          }
        }
      }
    }
  }
  .nav__underlay {
    display: none !important;
  }
}

@media screen and (max-width: 1140px) {
  .header {
    width: 300px;
    padding: 30px;
    overflow: auto;
    max-width: 100%;
    position: fixed;
    left: auto;
    bottom: 0;
    transform: translateX(100%);
    opacity: 0;
    transition: .6s;
    background-color: #fff;
    .container {
      padding: 0;
      position: static;
    }
  }
  .header__content {
    min-height: calc(100vh - 60px);
    padding-bottom: 100px;
    height: auto;
    display: block;
    position: relative;
    .header__part {
      display: block;
    }
  }
  .custom-logo-link {
    max-width: 150px;
    width: 100%;
  }
  .header__part {
    position: static;
    &:last-child {
      margin-top: 25px;
      display: block;
    }
  }
  .header__nav {
    margin-top: 25px;
    display: block;
    li {
      margin: 0;
      margin-bottom: 24px;
      a {
        color: #000;
        display: inline-block;
      }
      &.current-menu-item {
        a {
          color: #000 !important;
        }
      }
    }
  }
  .header__enter {
    margin: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 58px;
    max-width: 224px;
    width: 100%;

    &::before {
      width: 22px;
      height: 22px;
      margin-right: 15px;
      filter: none
    }
  }
  .headerenter__text {
    span {
      &:first-child {
        font-size: 12px;
        color: #808080;
      }
      &:last-child {
        font-size: 14px;
        color: #031C45;
      }
    }
  }
  .header__soc {
    margin-top: 44px;
    margin-left: 0;
  }
  .nav__close {
    width: 24px;
    height: 24px;
    top: 0;
    right: 0;
    transform: translateX(50%) translateY(-50%);
    display: block;
    position: absolute;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-background-size: contain;
    background-size: contain;
    background-image: url("data:image/svg+xml,%0A%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 50 50' width='50px' height='50px'%3E%3Cpath d='M 9.15625 6.3125 L 6.3125 9.15625 L 22.15625 25 L 6.21875 40.96875 L 9.03125 43.78125 L 25 27.84375 L 40.9375 43.78125 L 43.78125 40.9375 L 27.84375 25 L 43.6875 9.15625 L 40.84375 6.3125 L 25 22.15625 Z'/%3E%3C/svg%3E");
  }
  .nav__underlay {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 99;
    background-color: #000;
    opacity: 0;
    visibility: hidden;
    transition: .6s;
  }
  body.openedNav {
    overflow: hidden;
    .header {
      opacity: 1;
      transform: translateX(0);
    }
    .nav__underlay {
      opacity: .6;
      visibility: visible;
    }
  }
  .hamburger {
    top: 15px;
    right: 15px;
    width: 58px;
    height: 58px;
    background-color: rgba(0, 166, 156, .2);
    position: fixed;
    z-index: 90;
    display: flex;
    border-radius: 50%;
    align-items: center;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    span {
      width: 28px;
      height: 3px;
      margin-bottom: 5px;
      background-color: $turquoise;
      &:last-child {
        margin: 0;
      }
    }
  }
}


@media screen and (max-width: 767px) {
  .header {
    padding: 25px;
    padding-top: 30px;
  }
  .header__content {
    min-height: calc(100vh - 55px);
  }
  .nav__close {
    transform: translateX(50%) translateY(-50%);
  }
}