.section__account {
  padding-top: 174px;
  padding-bottom: 100px;
}

.accoun__title {
  font-size: 42px;
  font-weight: 600;
}

.account__content {
  margin-top: 38px;

  & > .woocommerce {
    justify-content: space-between;
    align-items: flex-start;
    align-content: flex-start;
    display: flex;
  }
}

.account__side {
  padding: 28px 34px;
  min-height: 624px;
  width: 33.53%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid #ECECEC;
  background-color: #fff;
}

.acount__main {
  width: 63.19%;

  .woocommerce-message {
    color: $green;
  }

  .woocommerce-error {
    color: $red !important;
  }

  .woocommerce-notices-wrapper {
    .error {
      color: $red !important;
    }

    .mess {
      color: $green;
    }
  }
}

.woocommerce-MyAccount-navigation-link {
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 1px solid #ECECEC;

  &.is-active {
    a {
      color: #808080;

      img {
        filter: invert(45%) sepia(96%) saturate(304%) hue-rotate(75deg) brightness(104%) contrast(92%);
      }
    }
  }

  a {
    font-size: 16px;
    line-height: 1.22;
    color: #000;
    padding-left: 34px;
    position: relative;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.acc__icon {
  width: 22px;
  height: 22px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.lastNav {
  .woocommerce-MyAccount-navigation-link {
    padding-bottom: 0;
    border: none;
  }
}

///////////////
.aritacc__title {
  font-size: 21px;
  font-weight: 600;
}

.woocommerce-EditAccountForm {
  padding: 36px;
  padding-top: 32px;
  margin-top: 17px;
  border: 1px solid #ECECEC;
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  justify-content: space-between;

  .woocommerce-form-row {
    width: calc(50% - 10px);
    padding-bottom: 15px;

    .error__mess {
      font-size: 12px;
      margin-top: 4px;
      display: none;
      color: $red;
    }

    &.error {
      input {
        border-color: $red;
      }

      .error__mess {
        display: block;
      }
    }

    label {
      font-size: 14px;
      margin-bottom: 6px;
      line-height: 1.2;
      display: block;
      color: #808080;
    }

    input {
      height: 55px;
      padding: 0 25px;
      font-size: 14px;
      border: 1px solid #D2D2D2;
      border-radius: 5px;
      display: block;
      width: 100%;
    }
  }
}

.edit__accbottom {
  margin-top: 20px;
  width: 100%;
  align-items: center;
  align-content: center;
  justify-content: space-between;

  .saveacc__none {
    display: none;
  }

  .button {
    height: 52px;
    width: 160px;
  }

  .changepass__btn {
    font-size: 15px;
    color: #031C45;
    text-decoration: underline;
  }
}

.password__popup {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 200;
  display: none;

  .overlay {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: #000;
    opacity: .6;
  }
}

.passpopup__content {
  width: 526px;
  max-width: calc(100% - 30px);
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  z-index: 2;
}

.postpop__inner {
  padding: 60px;
  max-height: calc(100vh - 30px);
  overflow: auto;
}

.passpopup__title {
  font-size: 21px;
  font-weight: 700;
  text-align: center;
  margin: 0;
}

