.test__container {
  height: 562px;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-direction: column;
}

.test__content {
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  transition: .3s;
  position: relative;
  transform: translateY(0);
  opacity: 1;

  &.hideTest {
    transform: translateY(30px);
    opacity: 0;
  }
}

.test__uppertitle {
  font-size: 16px;
  line-height: 1.3;
  color: #43AE4E;
  font-weight: 600;
}

.test__title {
  margin-top: 11px;
  font-size: 21px;
  line-height: 1.2;
  font-weight: 600;
  text-align: center;
}

.test__startbtn {
  margin-top: 60px;
  width: 182px;
  height: 45px !important;
}

.test__top {
  display: none;
}

.test__listcont {
  position: relative;
  transform: translateY(30px);
  opacity: 0;
  transition: .3s;
  height: 100%;
  flex-wrap: nowrap;
  width: 100%;
  text-align: center;

  &.showed {
    transform: translateY(0);
    opacity: 1;
  }
}

.test__form {
  padding: 30px 0;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 100%;
  max-width: 690px;
}

.test__question {
  opacity: 0;
  transform: translateY(30px);
  transition: .3s;
  display: none;

  &.activeQ {
    opacity: 1;
    transform: translateY(0);
  }

  &.activeQB {
    display: block;
  }
}

.test__number {
  text-align: center;
  font-size: 14px;
  line-height: 1.2;
  color: #43AE4E;
  font-weight: 600;
}

.test__name {
  margin-top: 12px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
}

.test_vars {
  margin-top: 48px;
  margin-bottom: -30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  text-align: left;
}

.test__var {
  margin-bottom: 30px;
  width: calc(50% - 15px);
}

.var__label {
  padding-left: 32px;
  font-size: 14px;
  line-height: 1.5;
  display: block;
  position: relative;

  input {
    display: none;

    &:checked {
      & + span {
        &::before {
          background-color: #43AE4E;
        }
      }
    }
  }

  span {
    display: block;

    &::before {
      width: 20px;
      height: 20px;
      -webkit-background-size: 12px;
      background-size: 12px;
      top: 11px;
      border: 1.5px solid #43AE4E;
      left: 0;
      transform: translateY(-50%);
      border-radius: 50%;
      transition: .3s;
      content: '';
      position: absolute;
      background-repeat: space;
      background-position: center center;
      background-image: url("data:image/svg+xml,%0A%3Csvg width='12' height='12' viewBox='0 0 12 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.25 3C11.25 3.19191 11.1768 3.38391 11.0303 3.53016L5.03027 9.53016C4.88438 9.67734 4.69219 9.75 4.5 9.75C4.30781 9.75 4.11609 9.67676 3.96984 9.53027L0.969844 6.53027C0.823359 6.38438 0.75 6.19219 0.75 6C0.75 5.57156 1.10039 5.25 1.5 5.25C1.69191 5.25 1.88391 5.32324 2.03016 5.46973L4.5 7.94062L9.97031 2.47031C10.1156 2.32336 10.3078 2.25 10.5 2.25C10.9008 2.25 11.25 2.57109 11.25 3Z' fill='white'/%3E%3C/svg%3E%0A");
    }
  }
}

.testans__button {
  margin: 0 auto;
  margin-top: 90px;
  width: 182px;
  transition: .3s;
  height: 45px !important;

  &.disabled {
    opacity: .6;
    cursor: inherit;
    background-color: #42AE4D !important;
    border-color: #42AE4D !important;
  }
}

.test__story {
  width: 100%;
}

.test__story {
  margin: 0 -4px;
  margin-bottom: -8px;
  justify-content: center;
}

.test__questnum {
  margin: 0 4px;
  margin-bottom: 8px;
  width: 27px;
  height: 27px;
  font-size: 12px;
  font-weight: 500;
  color: #808080;
  border: 1px solid #ECECEC;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;

  &.active {
    border: 1px solid #43AE4E;
  }

  &.true {
    border: 1px solid rgba(67, 174, 78, 0.3);
    color: rgba(128, 128, 128, 0.3);
  }

  &.false {
    border: 1px solid rgba(142, 0, 15, 0.3);
    color: rgba(128, 128, 128, 0.3);
  }
}

.test__result {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 600;
  text-align: center;
}

.test__restext {
  margin-top: 12px;
  font-size: 21px;
  line-height: 1.2;
  font-weight: 600;
  color: #43AE4E;
  text-align: center;
}

.test__endbtn {
  margin-top: 60px;
  width: 224px;
  height: 45px !important;
}

.test__successcont {
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.testsert__btn {
  width: 242px;
  height: 45px !important;
  margin-right: 24px;
}

.test__sertview {
  font-size: 14px;
  color: #43AE4E;
  text-decoration: underline;
}

.test__account {
  font-size: 14px;
  margin-top: 18px;
  line-height: 1.5;
  width: 100%;
  text-align: center;

  a {
    color: #1A325C;
    text-decoration: underline;
  }
}

.sertp__num {
  font-size: 14px;
  line-height: 1.5;
  color: #808080;
  text-align: center;
}

.sertp__title {
  margin-top: 11px;
  font-size: 18px;
  line-height: 1.2;
  color: #43AE4E;
  font-weight: 600;
  text-align: center;
}

.sertp__under {
  margin-top: 7px;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
}

.notaxx__top {
  font-size: 16px;
  line-height: 1.2;
  color: #43AE4E;
  font-weight: 600;
  text-align: center;
}

.notaxx__title {
  margin-top: 12px;
  font-size: 21px;
  line-height: 1.2;
  font-weight: 600;
  text-align: center;
}


@media screen and (min-width: 1240px) and (max-width: 1690px) {
  .test__container {
    height: calc(((100vw - 344px) / 100 * 66 / 16*9) + 70px);
  }
}


@media screen and (max-width: 1690px) {
  .test_vars {
    margin-top: 30px;
    margin-bottom: -20px;
  }
  .test__var {
    margin-bottom: 20px;
  }
  .testans__button {
    margin-top: 40px;
  }
}


@media screen and (max-width: 1240px) {
  .test__container {
    height: calc((70vw - 30px) /16*9 + 74px);
  }
}


@media screen and (max-width: 980px) {
  .test__container {
    height: auto;
  }
  .test__top {
    display: block;
  }

  .test__content {
    padding: 30px 0;
    min-height: 440px;
  }
  .test__listcont {
    padding: 0;
    height: auto;
  }
  .test__form {
    min-height: 370px;
  }
}


@media screen and (max-width: 767px) {
  .test__uppertitle {
    font-size: 14px;
    line-height: 1.7;
  }
  .test__title {
    margin-top: 13px;
    font-size: 17px;
    line-height: 1.5;
  }
  .test__startbtn {
    margin-top: 50px;
    width: 242px;
  }
  .test__form {
    padding: 40px 0;
    padding-bottom: 50px;
    min-height: 490px;
    width: 100%;
  }
  .test_vars {
    display: block;
  }
  .test__var {
    width: 100%;
  }
  .test__questnum {
    width: 26px;
    height: 25px;
  }
  .test__result {
    font-size: 14px;
    line-height: 1.5;
  }
  .test__restext {
    margin-top: 20px;
    font-size: 17px;
    line-height: 1.5;
  }
  .test__endbtn {
    margin-top: 44px;
  }
  .test__successcont {
    margin-top: 50px;
    flex-direction: column;
  }
  .testsert__btn {
    margin-right: 0;
  }
  .test__sertview {
    margin-top: 24px;
  }
  .test__account {
    margin-top: 28px;
    font-size: 12px;
  }
  .sertp__title {
    font-size: 17px;
  }
  .sertp__under {
    margin-top: 16px;
  }
  .notaxx__top {
    font-size: 12px;
  }
  .notaxx__title {
    margin-top: 4px;
    font-size: 17px;
    line-height: 1.5;
  }
}