.section__advent {
  padding: 30px 0;
  background-color: #001029;
}

.advent__list {
  margin: 0 -20px;
  margin-bottom: -20px;
}

.advent__item {
  width: calc(25% - 40px);
  margin: 0 20px;
  margin-bottom: 20px;
  padding: 8px 0;
  color: #fff;
}

.advent__name {
  font-size: 32px;
  padding-bottom: 22px;
  position: relative;
  line-height: 1.156;

  &::after {
    right: 20px;
    background-color: $green;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 1px;
  }
}

.advent__text {
  margin-top: 14px;
  max-width: 250px;
  font-size: 16px;
  font-weight: 300;

  a {
    color: $red;
  }
}


@media screen and (max-width: 1240px) {
  .advent__name {
    &::after {
      right: 0;
    }
  }
}


@media screen and (max-width: 980px) {
  .section__advent {
    padding: 50px 0;
  }
  .advent__item {
    width: calc(50% - 40px);
  }
}


@media screen and (max-width: 767px) {
  .advent__list {
    margin: 0;
  }
  .advent__item {
    width: 100%;
    margin: 0;
    margin-bottom: 20px;
    &:last-child {
      margin: 0;
    }
  }
  .advent__text {
    max-width: inherit;
  }
  .advent__name {
    font-size: 26px;
  }
}