.section__evabout {
  padding: 60px 0;
}

.evabout__cont {
  justify-content: space-between;
}

.evabout__part {
  width: 46.5%;
  flex-direction: column;
  justify-content: space-between;
  align-content: flex-start;
  align-items: flex-start;
}

.evabout__top {
  padding-bottom: 25px;
  position: relative;

  &::after {
    width: 50px;
    height: 3px;
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: $darkGreen;
  }
}

.evabout__title {
  font-size: 42px;
  color: $blue;
  font-weight: 600;
}

.evabout__short {
  font-size: 16px;
  margin-top: 14px;
  color: #000;
  opacity: 0.6;
}

.evabout__text {
  margin-top: 24px;
  font-size: 17px;

  &:first-child {
    margin-top: 12px;
  }
}

.evabout__btn {
  min-width: 224px;
  margin-top: 24px;
}


@media screen and (max-width: 980px) {
  .section__evabout {
    padding: 40px 0;
  }
  .evabout__part {
    width: 48%;
  }
  .evabout__top {
    padding-bottom: 12px;
  }
  .evabout__title {
    font-size: 38px;
  }
  .evabout__short {
    margin-top: 0;
  }
  .evabout__text {
    font-size: 16px;
    margin-top: 1em;
    &:first-child {
      margin-top: 14px;
    }
  }
}


@media screen and (max-width: 767px) {
  .evabout__part {
    width: 100%;
    align-items: center;
    align-content: center;
  }
  .evabout__text {
    &:first-child {
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 480px) {
  .evabout__top {
    padding-bottom: 16px;
  }
  .evabout__title {
    font-size: 30px;
  }
  .evabout__short {
    font-size: 14px;
    margin-top: 0;
  }
}